<template>
    <div class="server-search-panel">
        <div class="search-header">
            <span>
                <span>{{ '' }}<span class="strong number">{{ list.length }}</span>{{ 'IP' }}</span>
                <span v-if="statisticsData.fail"><span class="error">{{ statisticsData.fail }}</span>{{ 'Agent' }}</span>
            </span>
            <action-extend :list="list" copyable>
                <template v-if="editable">
                    <div class="action-item" @click="handleRemoveAll">{{ '' }}</div>
                    <div class="action-item" @click="handleRemoveFail">{{ '' }}</div>
                </template>
            </action-extend>
        </div>
        <host-table :list="list" editable is-search @on-change="handleRemoveOne" />
    </div>
</template>
<script>
       import HostTable from '../components/host-table';
    import ActionExtend from '../components/action-extend';
    import {
        sortHost,
        statisticsHost,
    } from '../components/utils';
    
    export default {
        name: '',
        components: {
            HostTable,
            ActionExtend,
        },
        props: {
            data: {
                type: Array,
                required: true,
            },
            editable: {
                type: Boolean,
                default: false,
            },
        },
        data () {
            return {
                list: [{},{},{}],
                statisticsData: {},
            };
        },
        computed: {
            classes () {
                return {
                    scroll: this.data.length > 10,
                };
            },
        },
        watch: {
            data: {
                handler (host) {
                    if (this.isInnerChange) {
                        this.isInnerChange = false;
                        return;
                    }
                    this.page = 1;
                    this.list = Object.freeze(sortHost(host));
                    this.statisticsData = statisticsHost(host);
                },
                immediate: true,
            },
        },

        methods: {
            /**
             * @desc 
             */
            handleRemoveAll () {
                if (this.list.length < 1) {
                    this.messageSuccess('');
                    return;
                }
                const allList = this.list;
                this.list = [];
                this.isInnerChange = true;
                this.$emit('on-change', allList);
                this.messageSuccess('');
            },
            /**
             * @desc 
             *
             * change
             */
            handleRemoveFail () {
                const effectiveIp = [];
                const failIp = [];
                this.list.forEach((currentHost) => {
                    if (currentHost.alive) {
                        effectiveIp.push(currentHost);
                    } else {
                        failIp.push(currentHost);
                    }
                });
                if (effectiveIp.length === this.list.length) {
                    this.messageSuccess('');
                    return;
                }
                this.list = Object.freeze(effectiveIp);
                this.isInnerChange = true;
                this.$emit('on-change', failIp);
                this.messageSuccess('');
            },
            /**
             * @desc 
             * @param {Number} hostRealId 
             *
             * change
             */
            handleRemoveOne (hostRealId) {
                // 
                const newList = [];
                let removeHost = null;
                this.list.forEach((currentHost) => {
                    if (currentHost.realId !== hostRealId) {
                        newList.push(currentHost);
                    } else {
                        removeHost = currentHost;
                    }
                });
                this.list = Object.freeze(newList);
                if (!removeHost) {
                    return;
                }
                
                this.isInnerChange = true;
                this.$emit('on-change', [
                    removeHost,
                ]);
            },
        },
    };
</script>
<style lang='postcss'>
    .server-search-panel {
        color: #63656e;
        border: 1px solid #dcdee5;

        .search-header {
            position: relative;
            height: 41px;
            padding-left: 60px;
            font-size: 12px;
            font-weight: bold;
            line-height: 41px;
            color: #63656e;
            background: #fafbfd;
            border-bottom: 1px solid #dcdee5;
        }

        .list-more {
            border-top: 1px solid #dcdee5;
        }

        .search-empty {
            height: 240px;
        }
    }
</style>
