<template>
    <jb-form-item :label="''" required>
        <div class="form-item-content">
            <div class="file-step-transfer-mode-wraper">
                <bk-radio-group class="radio-check" :value="formData[field]" @change="handleChange">
                    <bk-radio v-bk-tooltips="constraintTips" :value="2">{{ '' }}</bk-radio>
                    <bk-radio v-bk-tooltips="strictTips" :value="1">{{ '' }}</bk-radio>
                </bk-radio-group>
            </div>
        </div>
    </jb-form-item>
</template>
<script>
   
    export default {
        name: '',
        props: {
            field: {
                type: String,
                required: true,
            },
            formData: {
                type: Object,
                default: () => ({}),
            },
        },
        data () {
            return {
                radioValue: 2,
            };
        },
        computed: {
            // radio -1
            // 
            showSelectCheck () {
                return this.radioValue === -1;
            },
        },
        watch: {
            formData: {
                // 34
                handler  (formData) {
                    const transferMode = parseInt(formData[this.field], 10);
                    if ([
                        3,
                        4,
                    ].includes(transferMode)) {
                        this.radioValue = -1;
                    } else {
                        this.radioValue = transferMode;
                    }
                },
                immediate: true,
                deep: true,
            },
        },
        created () {
            this.strictTips = {
                content: '',
                width: 180,
            };
            this.constraintTips = {
                content: '',
                width: 210,
            };
            this.safeTips = {
                content: '',
                width: 210,
            };
        },
        methods: {
            handleChange (transferMode) {
                const realValue = transferMode === -1 ? 3 : transferMode;
                this.$emit('on-change', this.field, realValue);
            },
            handleSelectChange (transferMode) {
                this.$emit('on-change', this.field, transferMode);
            },
        },
    };
</script>
<style lang="postcss">
    .file-step-transfer-mode-wraper {
        display: flex;
        align-items: center;
        height: 32px;

        .radio-check {
            flex: 0 0 auto;
            width: auto;

            .bk-form-radio {
                &:nth-child(n+2) {
                    margin-left: 32px;
                }
            }

            .bk-radio-text {
                border-bottom: 1px dashed #c4c6cc;
            }
        }

        .select-check {
            flex: 1;
            min-width: 295px;
            margin-left: 12px;
        }
    }
</style>
