<template>
    <div class="global-bariable-batch-operation">
        <table>
            <thead>
                <tr>
                    <th style="width: 130px;">{{ '' }}<span class="require-flag" /></th>
                    <th>{{ '' }}<span class="require-flag" /></th>
                    <th>
                        <span v-bk-tooltips="' []'" class="hover-tips">
                            {{ '' }}
                        </span>
                    </th>
                    <th style="width: 320px;">{{ '' }}</th>
                    <th style="width: 80px;">
                        <span v-bk-tooltips="''" class="hover-tips">
                            {{ '' }}
                        </span>
                    </th>
                    <th style="width: 100px;">{{ '' }}</th>
                    <th style="width: 80px;">{{ '' }}</th>
                </tr>
            </thead>
            <template v-for="(variableItem, index) in variableList">
                <render-table-row
                    v-if="variableItem.id > 0 && variableItem.delete !== 1"
                    ref="variableEdit"
                    :variable-name-list="calcExcludeNameList(variableItem)"
                    :data="variableItem"
                    :key="variableItem.id"
                    @on-change="value => handleChange(index, value)"
                    @on-delete="handleDelete(index)"
                    @on-append="handleAppendVariable(index)" />
                <!-- ,  -->
                <create-table-row
                    v-else-if="variableItem.id < 0"
                    ref="variableCreate"
                    :key="variableItem.id"
                    :variable-name-list="calcExcludeNameList(variableItem)"
                    :data="variableItem"
                    @on-change="value => handleChange(index, value)"
                    @on-delete="handleDelete(index)"
                    @on-append="handleAppendVariable(index)" />
            </template>
        </table>
        <div
            v-if="isEmpty"
            class="empty-box"
            @click="handleAppendVariable(0)">
            <Icon type="add-fill" />
            <span>{{ '' }}</span>
        </div>
    </div>
</template>
<script>
    import _ from 'lodash';
    import GlobalVariableModel from '@model/task/global-variable';
    import RenderTableRow from './render-table-row.vue';
    import CreateTableRow from './create-table-row.vue';
    import { createVariable } from '../util';

    export default {
        name: '',
        components: {
            RenderTableRow,
            CreateTableRow,
        },
        props: {
            variable: {
                type: Array,
                default: () => [],
            },
        },
        data () {
            return {
                variableList: _.cloneDeep(this.variable),
            };
        },
        computed: {
            isEmpty () {
                // eslint-disable-next-line no-plusplus
                for (let i = 0; i < this.variableList.length; i++) {
                    if (!this.variableList[i].delete) {
                        return false;
                    }
                }
                return true;
            },
        },
        methods: {
            /**
             * @desc 
             * @param { Object } variableData 
             * @returns { Array }
             *
             * 
             */
            calcExcludeNameList (variableData) {
                return this.variableList.reduce((result, item) => {
                    if (variableData.id !== item.id
                        && item.name
                        && item.delete !== 1) {
                        result.push(item.name);
                    }
                    return result;
                }, []);
            },
            /**
             * @desc 
             * @param {Number} index 
             * @param {Object} variableData 
             */
            handleChange (index, variableData) {
                const variableList = [...this.variableList];
                const variable = new GlobalVariableModel(variableData);
                variableList.splice(index, 1, variable);
                this.variableList = variableList;
                window.changeAlert = true;
            },
            /**
             * @desc 
             * @param {Number} index 
             */
            handleDelete (index) {
                const variableList = [...this.variableList];
                const editVariable = variableList[index];
                if (editVariable.id > 0) {
                    // delete
                    editVariable.delete = 1;
                } else {
                    // 
                    variableList.splice(index, 1);
                }
                this.variableList = variableList;
                window.changeAlert = true;
            },
            /**
             * @desc 
             * @param {Number} index 
             */
            handleAppendVariable (index) {
                this.variableList.splice(index + 1, 0, createVariable());
                window.changeAlert = true;
            },
            /**
             * @desc 
             * @returns {Promise}
             */
            submit () {
                const queue = [];
                if (this.$refs.variableEdit) {
                    queue.push(...this.$refs.variableEdit.map(item => item.validate()));
                }
                if (this.$refs.variableCreate) {
                    queue.push(...this.$refs.variableCreate.map(item => item.validate()));
                }
                return Promise.all(queue)
                    .then(() => this.$emit('on-change', this.variableList));
            },
        },
    };
</script>
<style lang="postcss">
    .global-bariable-batch-operation {
        table {
            width: 100%;
            font-size: 12px;
            line-height: 18px;
            color: #63656e;
            border: 1px solid #dcdee5;
            border-radius: 2px;
            table-layout: fixed;

            thead {
                background: #fafbfd;
            }

            th,
            td {
                height: 41px;
                padding-right: 5px;
                padding-left: 15px;
                text-align: left;
            }

            th {
                font-weight: normal;
                color: #313238;
            }

            td {
                padding-top: 5px;
                padding-bottom: 5px;
                border-top: 1px solid #dcdee5;
            }

            .hover-tips {
                padding-bottom: 2px;
                border-bottom: 1px dashed #c4c6cc;
            }

            .require-flag {
                &::after {
                    display: inline-block;
                    height: 8px;
                    font-size: 12px;
                    line-height: 1;
                    color: #ea3636;
                    vertical-align: middle;
                    content: "*";
                }
            }

            .action-row {
                user-select: none;

                .action-btn {
                    font-size: 18px;
                    color: #c4c6cc;
                    cursor: pointer;
                }
            }
        }

        .empty-box {
            display: flex;
            height: 32px;
            margin-top: 6px;
            font-size: 12px;
            color: #979ba5;
            cursor: pointer;
            background: #fcfdff;
            border: 1px dashed #c4c6cc;
            border-radius: 2px;
            align-items: center;
            justify-content: center;

            i {
                margin-right: 7px;
                font-size: 14px;
                color: #c4c6cc;
            }

            &:hover {
                color: #3a84ff;
                border-color: #3a84ff;

                i {
                    color: #3a84ff;
                }
            }
        }
    }
</style>
