<template>
    <div class="exec-script-view" :class="{ loading: isLoading }" v-bkloading="{ isLoading }">
        <detail-item :label="''">
            {{ stepInfo.scriptSourceText }}
        </detail-item>
        <detail-item v-if="isReferScript" :label="''">
            <span>{{ scriptName }}</span>
            <Icon
                class="script-detail"
                type="jump"
                v-bk-tooltips="''"
                @click="handleGoScriptDetail" />
            <div
                v-if="stepInfo.isNeedUpdate || stepInfo.isDisabled"
                class="script-update-flag">
                <span v-html="stepInfo.scriptStatusHtml" />
                <bk-button
                    v-if="stepInfo.isNeedUpdate"
                    text
                    @click="handleGoScriptVersion">
                    {{ '' }}
                </bk-button>
                <bk-button text @click="handleUpdateScript">
                    {{ '' }}
                </bk-button>
            </div>
        </detail-item>
        <detail-item :label="''" layout="vertical">
            <ace-editor
                :value="stepInfo.content"
                :lang="language"
                :options="languageOption"
                readonly />
        </detail-item>
        <div>
            <detail-item :label="''">
                <jb-edit-textarea field="scriptParam" :value="stepInfo.scriptParam" readonly />
            </detail-item>
            <detail-item :label="''">{{ stepInfo.timeout }}s</detail-item>
            <detail-item :label="''">{{ stepInfo.ignoreErrorText }}</detail-item>
            <detail-item :label="''">{{ executeAccountText }}</detail-item>
        </div>
        <detail-item v-if="stepInfo.executeTarget.variable" :label="''">
            <render-global-variable
                :type="''"
                :name="stepInfo.executeTarget.variable"
                :data="variable" />
        </detail-item>
        <detail-item v-else :label="''" layout="vertical">
            <server-panel
                detail-fullscreen
                :host-node-info="stepInfo.executeTarget.hostNodeInfo" />
        </detail-item>
    </div>
</template>
<script>
    import ScriptService from '@service/script-manage';
    import AccountManageService from '@service/account-manage';
    import AceEditor from '@components/ace-editor';
    import ServerPanel from '@components/choose-ip/server-panel';
    import DetailItem from '@components/detail-layout/item';
    import JbEditTextarea from '@components/jb-edit/textarea';
    import RenderGlobalVariable from './components/render-global-variable';
    import {
        formatScriptTypeValue,
    } from '@utils/assist';

    export default {
        name: '',
        components: {
            AceEditor,
            ServerPanel,
            DetailItem,
            JbEditTextarea,
            RenderGlobalVariable,
            
        },
        props: {
            data: {
                type: Object,
                default: () => ({}),
            },
            variable: {
                type: Array,
                default: () => [],
            },
        },
        data () {
            return {
                stepInfo: {},
                executeAccountText: '',
                language: '',
                scriptName: '',
                scriptInfo: {},
                isShowScriptSource: false,
                requestQueue: [],
            };
        },
        computed: {
            isLoading () {
                return this.requestQueue.length > 0;
            },
            isReferScript () {
                return this.data.scriptStepInfo.scriptSource && this.data.scriptStepInfo.scriptSource !== 1;
            },
        },
        created () {
            this.stepInfo = Object.freeze(this.data.scriptStepInfo);
            this.language = formatScriptTypeValue(this.stepInfo.scriptLanguage);
            this.languageOption = [
                this.language,
            ];
            if (this.stepInfo.scriptVersionId) {
                this.fetchScriptDetail();
            }
            this.fetchAccount();
        },
        methods: {
            /**
             * @desc 
             */
            fetchScriptDetail () {
                this.requestQueue.push(true);
                ScriptService.versionDetail({
                    id: this.stepInfo.scriptVersionId,
                }).then((data) => {
                    this.scriptName = data.name;
                    this.stepInfo.content = data.content;
                    this.scriptInfo = Object.freeze(data);
                })
                    .finally(() => {
                        this.requestQueue.pop();
                    });
            },
            /**
             * @desc 
             */
            fetchAccount () {
                this.requestQueue.push(true);
                AccountManageService.fetchAccountWhole()
                    .then((data) => {
                        const accountData = data.find(item => item.id === this.stepInfo.account);
                        if (accountData) {
                            this.executeAccountText = accountData.alias;
                        } else {
                            this.executeAccountText = '--';
                        }
                    })
                    .finally(() => {
                        this.requestQueue.pop();
                    });
            },
            /**
             * @desc 
             */
            handleGoScriptDetail () {
                const routerName = this.scriptInfo.publicScript ? 'publicScriptVersion' : 'scriptVersion';

                const router = this.$router.resolve({
                    name: routerName,
                    params: {
                        id: this.stepInfo.scriptId,
                    },
                    query: {
                        scriptVersionId: this.scriptInfo.scriptVersionId,
                    },
                });
                window.open(router.href);
            },
            /**
             * @desc 
             */
            handleGoScriptVersion () {
                const routerName = this.isReferPublicScript ? 'publicScriptVersion' : 'scriptVersion';
                const { href } = this.$router.resolve({
                    name: routerName,
                    params: {
                        id: this.stepInfo.scriptId,
                    },
                });
                window.open(href);
            },
            /**
             * @desc 
             */
            handleUpdateScript () {
                this.$router.push({
                    name: 'templateEdit',
                    params: {
                        id: this.$route.params.id,
                        stepId: this.data.id,
                    },
                    query: {
                        from: 'templateDetail',
                    },
                });
            },
        },
    };
</script>
<style lang="postcss">
    .exec-script-view {
        &.loading {
            height: calc(100vh - 100px);
        }

        .detail-item {
            margin-bottom: 0;
        }

        .script-detail {
            color: #3a84ff;
            cursor: pointer;
        }

        .script-update-flag {
            display: inline-block;

            .script-update {
                color: #ff5656;
            }
        }
    }
</style>
