<template>
    <div class="render-server-agent">
        <Icon
            v-if="isLoading"
            class="rotate-loading"
            svg
            type="sync-pending" />
        <div
            v-else
            class="agent-text"
            @click="handleShowDetail">
            <div v-if="data.normalNum > 0">
                {{ '' }}:<span class="success number">{{ data.normalNum }}</span>
            </div>
            <div v-if="data.abnormalNum > 0">
                <span v-if="data.normalNum > 0" class="splite" />
                {{ '' }}:<span class="error number">{{ data.abnormalNum }}</span>
            </div>
            <span v-if="isEmpty">--</span>
        </div>
        <lower-component level="custom" :custom="isShowDetail">
            <jb-dialog
                v-model="isShowDetail"
                :width="1020"
                :ok-text="''"
                class="render-server-detail-dialog">
                <template #header>
                    <div class="variable-title">
                        <span>{{ title }}</span>
                        <i class="global-variable-dialog-close bk-icon icon-close" @click="handleClose" />
                    </div>
                </template>
                <div class="content-wraper">
                    <scroll-faker>
                        <server-panel
                            detail-mode="dialog"
                            :host-node-info="hostNodeInfo" />
                    </scroll-faker>
                </div>
            </jb-dialog>
        </lower-component>
    </div>
</template>
<script>
    import AppManageService from '@service/app-manage';
    import TaskHostNodeModel from '@model/task-host-node';
    import ServerPanel from '@components/choose-ip/server-panel';
    
    export default {
        name: '',
        components: {
            ServerPanel,
        },
        props: {
            title: {
                type: String,
                required: true,
            },
            hostNodeInfo: {
                type: Object,
                required: true,
            },
            separator: {
                type: String,
                default: '',
            },
        },
        data () {
            return {
                isLoading: false,
                isShowDetail: false,
                data: {
                    normalNum: 0,
                    abnormalNum: 0,
                },
            };
        },
        computed: {
            isEmpty () {
                return TaskHostNodeModel.isHostNodeInfoEmpty(this.hostNodeInfo);
            },
        },
        watch: {
            hostNodeInfo () {
                this.fetchData();
            },
        },
        created () {
            this.fetchData();
        },
        methods: {
            fetchData () {
                if (this.isEmpty || this.isLoading) {
                    this.isLoading = false;
                    return;
                }
                this.isLoading = true;
                const { dynamicGroupList, ipList, topoNodeList } = this.hostNodeInfo;
                AppManageService.fetchHostStatistics({
                    appTopoNodeList: topoNodeList.map(topo => ({
                        objectId: topo.type,
                        instanceId: topo.id,
                    })),
                    dynamicGroupIds: dynamicGroupList,
                    ipList: ipList.map(host => `${host.cloudAreaInfo.id}:${host.ip}`),
                }).then((data) => {
                    this.data = data;
                })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            handleShowDetail () {
                this.isShowDetail = true;
            },
            handleClose () {
                this.isShowDetail = false;
            },
        },
    };
</script>
<style lang="postcss">
    .render-server-agent {
        .agent-text {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            cursor: pointer;

            .strong {
                color: #3a84ff;
            }

            .error {
                color: #ea3636;
            }

            .success {
                color: #3fc06d;
            }

            .number {
                padding: 0 4px;
                font-weight: bold;
            }

            .splite {
                padding-left: 16px;
            }
        }
    }

    .render-server-detail-dialog {
        .bk-dialog-tool {
            display: none;
        }

        .bk-dialog-header,
        .bk-dialog-footer {
            position: relative;
            z-index: 99999;
            background: #fff;
        }

        .bk-dialog-header {
            padding: 0;
        }

        .bk-dialog-wrapper .bk-dialog-header .bk-dialog-header-inner {
            font-size: 20px;
            color: #000;
            text-align: left;
        }

        .bk-dialog-wrapper .bk-dialog-body {
            padding: 0;

            .server-panel {
                height: 100%;

                &.show-detail {
                    overflow: hidden;
                }

                .host-detail.show {
                    padding-left: 20%;
                }
            }
        }

        .content-wraper {
            height: 450px;
            max-height: 450px;
            min-height: 450px;
            margin-top: -1px;
        }

        button[name="cancel"] {
            display: none;
        }

        .variable-title {
            position: relative;
            height: 68px;
            padding-top: 0;
            padding-bottom: 0;
            padding-left: 25px;
            font-size: 20px;
            line-height: 68px;
            color: #000;
            text-align: left;
            border-bottom: 1px solid #dcdee5;
        }

        .global-variable-dialog-close {
            position: absolute;
            top: 0;
            right: 0;
            font-size: 32px;
            color: #c4c6cc;
            cursor: pointer;
            transition: all 0.15s;

            &:hover {
                transform: rotateZ(90deg);
            }
        }
    }
</style>
