<template>
    <div
        class="task-step-execute-target"
        :class="{
            'only-host': mode === 'onlyHost',
        }">
        <jb-form-item
            :label="''"
            required
            ref="targetServerRef"
            :property="property"
            :rules="rules">
            <div style="display: flex;">
                <template v-if="mode === 'onlyHost'">
                    <!--  -->
                    <bk-button class="mr10" @click="handleShowChooseIp">
                        <Icon type="plus" />
                        {{ '' }}
                    </bk-button>
                </template>
                <template v-else>
                    <!--  -->
                    <compose-form-item>
                        <bk-select
                            :style="targetSelectorStyle"
                            :value="targetType"
                            :clearable="false"
                            @change="handleTargetTypeChange">
                            <bk-option id="variable" :name="''" />
                            <bk-option id="hostNodeInfo" :name="''" />
                        </bk-select>
                        <template v-if="isGolbalVariableType">
                            <bk-select
                                class="server-global-variable-select"
                                :placeholder="''"
                                :value="localVariable"
                                @change="handleVariableChange"
                                :clearable="false">
                                <bk-option
                                    v-for="(item, index) in variable"
                                    :key="index"
                                    :id="item.name"
                                    :name="item.name" />
                            </bk-select>
                        </template>
                        <template v-else>
                            <bk-button class="w120 mr10" @click="handleShowChooseIp">
                                <Icon type="plus" />
                                {{ '' }}
                            </bk-button>
                        </template>
                    </compose-form-item>
                </template>
                <template v-if="isShowServerAction">
                    <bk-dropdown-menu>
                        <bk-button class="mr10" type="primary" slot="dropdown-trigger">
                            <span>{{ 'IP' }}</span>
                            <Icon type="down-small" class="action-flag" />
                        </bk-button>
                        <ul class="bk-dropdown-list" slot="dropdown-content">
                            <li><a @click="handleCopyAll">{{ 'IP' }}</a></li>
                            <li><a @click="handleCopyFail">{{ 'IP' }}</a></li>
                        </ul>
                    </bk-dropdown-menu>
                    <bk-button class="mr10" @click="handleClearAll">
                        <span>{{ '' }}</span>
                    </bk-button>
                    <bk-button type="primary" @click="handleRefreshHost">
                        {{ '' }}
                    </bk-button>
                </template>
                <bk-input
                    v-if="isShowHostSearchInput"
                    class="ip-search"
                    :value="searchText"
                    :placeholder="''"
                    right-icon="bk-icon icon-search"
                    @change="handleHostSearch" />
            </div>
            <lower-component level="custom" :custom="isShowServerPanel">
                <server-panel
                    v-show="isShowServerPanel"
                    ref="serverPanel"
                    class="view-server-panel"
                    :host-node-info="localHost"
                    :search-mode="Boolean(searchText)"
                    :search-data="searchData"
                    editable
                    @on-change="handleHostChange" />
            </lower-component>
        </jb-form-item>
        <choose-ip
            v-model="isShowChooseIp"
            :host-node-info="localHost"
            @on-change="handleHostChange" />
    </div>
</template>
<script>
    import _ from 'lodash';
    import TaskHostNodeModel from '@model/task-host-node';
       import { execCopy } from '@utils/assist';
    import ComposeFormItem from '@components/compose-form-item';
    import ChooseIp from '@components/choose-ip';
    import ServerPanel from '@components/choose-ip/server-panel';

    export default {
        components: {
            ComposeFormItem,
            ChooseIp,
            ServerPanel,
        },
        inheritAttrs: false,
        props: {
            taskHostNode: {
                type: Object,
                required: true,
            },
            variable: {
                type: Array,
                default: () => [],
            },
            property: {
                type: String,
            },
            mode: {
                type: String,
                default: '', // onlyHost: 
            },
        },
        data () {
            return {
                isShowChooseIp: false,
                searchText: '',
                isSearchMode: false,
                searchData: [],
                targetType: 'variable', // variablehostNodeInfo
                localVariable: '',
                localHost: {},
            };
        },
        computed: {
            /**
             * @desc 
             * @returns {Boolean}
             */
            isGolbalVariableType () {
                return this.targetType === 'variable';
            },
            /**
             * @desc 
             * @returns {Boolean}
             */
            isShowServerPanel () {
                if (this.isGolbalVariableType) {
                    return false;
                }
                return !TaskHostNodeModel.isHostNodeInfoEmpty(this.localHost);
            },
            /**
             * @desc 
             * @returns {Boolean}
             */
            isShowServerAction () {
                if (this.isGolbalVariableType) {
                    return false;
                }
                return !TaskHostNodeModel.isHostNodeInfoEmpty(this.localHost);
            },
            /**
             * @desc 
             * @returns {Boolean}
             */
            isClearFailDisabled () {
                return this.localHost.ipList.length < 1;
            },
            /**
             * @desc 
             * @returns {Boolean}
             */
            isShowHostSearchInput () {
                if (this.isGolbalVariableType) {
                    return false;
                }
                return this.localHost.ipList.length > 0;
            },
            /**
             * @desc 
             * @returns {Object}
             */
            targetSelectorStyle () {
                return {
                    width: 'zh-CN' === 'en-US' ? '156px' : '120px',
                };
            },
        },
        watch: {
            taskHostNode: {
                handler (taskHostNode) {
                    const {
                        hostNodeInfo,
                        variable,
                    } = taskHostNode;

                    this.localHost = hostNodeInfo;
                    this.localVariable = variable;
                    if (this.mode !== 'onlyHost') {
                        // 
                        if (!TaskHostNodeModel.isHostNodeInfoEmpty(this.localHost)) {
                            this.targetType = 'hostNodeInfo';
                        }
                    } else {
                        this.targetType = 'hostNodeInfo';
                    }
                },
                immediate: true,
            },
        },
        created () {
            // 
            if (this.isGolbalVariableType) {
                if (this.localVariable) {
                    // 
                    // 
                    // 
                    if (!this.variable.find(_ => _.name === this.localVariable)) {
                        setTimeout(() => {
                            this.handleVariableChange('');
                        });
                    }
                } else {
                    // 
                    if (this.variable.length > 0) {
                        setTimeout(() => {
                            this.handleVariableChange(this.variable[0].name);
                        });
                    }
                }
            }
            
            this.rules = [
                {
                    validator: () => {
                        if (this.isGolbalVariableType) {
                            return Boolean(this.localVariable);
                        }
                        return !TaskHostNodeModel.isHostNodeInfoEmpty(this.localHost);
                    },
                    message: '',
                    trigger: 'change',
                },
            ];
        },
        methods: {
            /**
             * @desc 
             */
            triggerChange () {
                const taskHostNode = new TaskHostNodeModel({});
                if (this.isGolbalVariableType) {
                    taskHostNode.variable = this.localVariable;
                } else {
                    taskHostNode.hostNodeInfo = this.localHost;
                }
                if (!taskHostNode.isEmpty) {
                    this.$refs.targetServerRef.clearValidator();
                }
                this.$emit('on-change', Object.freeze(taskHostNode));
            },
            /**
             * @desc 
             */
            handleTargetTypeChange (value) {
                this.targetType = value;
                this.triggerChange();
            },
            /**
             * @desc ip
             */
            handleShowChooseIp () {
                this.isShowChooseIp = true;
                this.searchText = '';
            },
            /**
             * @desc 
             * @param {String} value 
             */
            handleVariableChange (value) {
                this.localVariable = value;
                this.triggerChange();
            },
            /**
             * @desc 
             * @param {Object} hostNodeInfo 
             */
            handleHostChange (hostNodeInfo) {
                this.localHost = Object.freeze(hostNodeInfo);
                this.triggerChange();
            },
            /**
             * @desc 
             */
            handleCopyAll () {
                const allIP = this.$refs.serverPanel.getAllIP();
                if (allIP.length < 1) {
                    this.messageWarn('');
                    return;
                }
                
                execCopy(allIP.join('\n'), `${''}${allIP.length}${'IP'}`);
            },
            /**
             * @desc 
             */
            handleCopyFail () {
                const allFailIP = this.$refs.serverPanel.getAllIP(true);
                if (allFailIP.length < 1) {
                    this.messageWarn('');
                    return;
                }
                
                execCopy(allFailIP.join('\n'), `${''}${allFailIP.length}${'IP'}`);
            },
            /**
             * @desc 
             */
            handleClearAll () {
                const { hostNodeInfo } = new TaskHostNodeModel({});
                this.localHost = Object.freeze(hostNodeInfo);
                this.messageSuccess('');
                this.triggerChange();
            },
            /**
             * @desc 
             */
            handleRefreshHost () {
                this.$refs.serverPanel.refresh();
            },
            /**
             * @desc 
             * @param {String} search 
             */
            handleHostSearch (search) {
                this.searchText = _.trim(search);
                this.searchData = Object.freeze(this.$refs.serverPanel.getAllHost(search));
            },
        },
    };
</script>
<style lang='postcss'>
    html[lang="en-US"] {
        .compose-form-item {
            .server-global-variable-select {
                width: 341px;
            }
        }

        .ip-search {
            width: 162px;
        }

        .only-host {
            .ip-search {
                width: 314px;
            }
        }
    }

    .task-step-execute-target {
        &.only-host {
            .ip-search {
                width: 620px;
            }
        }

        .bk-button {
            font-size: 14px !important;
        }

        .action-flag {
            font-size: 18px;
        }

        .view-server-panel {
            margin-top: 14px;
        }

        .ip-search {
            flex: 0 0 auto;
            width: 245px;
            margin-left: auto;
        }

        .compose-form-item {
            .server-global-variable-select {
                width: 376px;
            }
        }
    }

    .execute-target-host-clear {
        user-select: none;

        .disabled {
            a {
                color: #c4c6cc !important;
                cursor: not-allowed;
                background-color: #fafafa !important;
            }
        }
    }
</style>
