<template>
    <div class="sleect-source-file">
        <list-action-layout>
            <jb-breadcrumb :width="645">
                <jb-breadcrumb-item>
                    <Icon type="folder-open" style="font-size: 20px;" />
                    <span>{{ '' }}</span>
                </jb-breadcrumb-item>
            </jb-breadcrumb>
            <template #right>
                <jb-input
                    :placeholder="''"
                    right-icon="bk-icon icon-search"
                    style="width: 480px;"
                    @change="handleSearch" />
            </template>
        </list-action-layout>
        <bk-table
            :data="tableData"
            max-height="500"
            :highlight-current-row="true"
            v-bkloading="{ isLoading }">
            <bk-table-column width="50">
                <template slot-scope="{ row }">
                    <span v-html="row.publicFlagHtml" />
                </template>
            </bk-table-column>
            <bk-table-column
                :label="''"
                prop="alias"
                sortable>
                <template slot-scope="{ row }">
                    <auth-button
                        v-if="row.isAvailable"
                        auth="file_source/view"
                        :permission="row.canView"
                        :resource-id="row.id"
                        text
                        @click="handleGoBucket(row)">
                        {{ row.alias }}
                    </auth-button>
                    <span
                        v-else
                        v-bk-tooltips="''">
                        <bk-button disabled text>{{ row.alias }}</bk-button>
                    </span>
                </template>
            </bk-table-column>
            <bk-table-column
                :label="''"
                prop="code"
                show-overflow-tooltip
                sortable />
            <bk-table-column
                :label="''"
                prop="status">
                <template slot-scope="{ row }">
                    <Icon :type="row.statusIcon" svg />
                    {{ row.statusText }}
                </template>
            </bk-table-column>
            <bk-table-column
                :label="''"
                prop="create_time">
                <template slot-scope="{ row }">
                    {{ row.storageTypeText }}
                </template>
            </bk-table-column>
            <bk-table-column
                :label="''"
                prop="creator" />
            <bk-table-column
                :label="''"
                prop="lastModifyTime" />
        </bk-table>
    </div>
</template>
<script>
    import FileSourceManageService from '@service/file-source-manage';
    import ListActionLayout from '@components/list-action-layout';
    
    export default {
        components: {
            ListActionLayout,
        },
        data () {
            return {
                isLoading: false,
                tableData: [],
                searchParams: [],
            };
        },
        created () {
            this.fetchData();
        },
        methods: {
            /**
             * @desc 
             */
            fetchData () {
                this.isLoading = true;
                FileSourceManageService.availableSourceList(this.searchParams).then(({ data }) => {
                    this.tableData = Object.freeze(data);
                })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },

            /**
             * @desc 
             * @param {String} alias 
             *
             * 
             */
            handleSearch (alias) {
                this.searchParams = {
                    alias,
                };
                this.fetchData();
            },

            /**
             * @desc bucket
             * @param {Object} fileSource 
             *
             * 
             */
            handleGoBucket (fileSource) {
                this.$emit('on-source-change', fileSource);
            },
        },
    };
</script>
<style lang="postcss">
    .sleect-source-file {
        padding-top: 14px;

        .list-action-layout {
            margin-bottom: 14px;
        }
    }
</style>
