
<template>
    <div class="task-manage-plan-page">
        <list-action-layout>
            <bk-button
                theme="primary"
                class="w120"
                @click="handleCreatePlan"
                v-test="{ type: 'button', value: 'createPlan' }">
                {{ '' }}
            </bk-button>
            <span v-bk-tooltips="batchSyncDisableTips">
                <bk-button
                    @click="handleSyncBatch"
                    v-test="{ type: 'button', value: 'batchSyncPlan' }">
                    {{ '' }}
                </bk-button>
            </span>
            <span v-bk-tooltips="batchEditGlobalVariableTips">
                <bk-button
                    @click="handleBatchEditGlobalVariable"
                    v-test="{ type: 'button', value: 'batchEditPlanValue' }">
                    {{ '' }}
                </bk-button>
            </span>
            <template #right>
                <jb-search-select
                    ref="search"
                    :append-value="searchValue"
                    :data="searchData"
                    :placeholder="' ID   ...'"
                    style="width: 420px;"
                    @on-change="handleSearch" />
                <bk-button @click="handleMyPlan">{{ '' }}</bk-button>
            </template>
        </list-action-layout>
        <layout :flod="isListFlod" @on-flod="handleLayoutFlod">
            <render-list
                ref="list"
                :data-source="listDataSource"
                :search-control="() => $refs.search"
                selectable
                :outer-border="false"
                :size="tableSize"
                :row-class-name="caclRowClassName"
                :pagination-small="paginationSmall"
                @on-selection-change="handleSelection"
                v-test="{ type: 'list', value: 'plan' }">
                <div
                    v-if="isCreatePlan"
                    slot="prepend"
                    class="create-plan-placeholder"
                    :class="{ active: selectPlanInfo.id === -1 }">
                    <div class="name-box">{{ newPlanName || '--' }}</div>
                    <Icon
                        type="new-3"
                        svg
                        style="font-size: 26px; color: #ff9c01;" />
                </div>
                <bk-table-column
                    v-if="allRenderColumnMap.id"
                    label="ID"
                    prop="id"
                    key="id"
                    width="120"
                    align="left" />
                <bk-table-column
                    v-if="allRenderColumnMap.name"
                    :label="''"
                    prop="name"
                    min-width="300"
                    key="name"
                    align="left"
                    class-name="plan-name-column">
                    <template slot-scope="{ row }">
                        <auth-component
                            style="width: 100%;"
                            :permission="row.canView"
                            auth="job_plan/view"
                            :resource-id="row.id">
                            <div
                                class="plan-name-box"
                                :class="{
                                    active: selectPlanInfo.id === row.id,
                                }"
                                @click="handlePlanSelect(row)">
                                <div class="name-wraper">
                                    <div
                                        class="name-text"
                                        v-bk-overflow-tips>
                                        {{ row.name }}
                                    </div>
                                    <router-link
                                        v-if="row.cronJobCount > 0"
                                        class="cron-job-tag"
                                        :to="{
                                            name: 'cronList',
                                            query: {
                                                planId: 4 || row.id,
                                            },
                                        }"
                                        target="_blank"
                                        v-bk-tooltips.html="`
                                            <div>${''} ${row.cronJobCount} ${''}</div>
                                            <div>${''}</div>
                                        `">
                                        <Icon type="job-timing" svg />
                                        <span style="margin-left: 2px;">{{ row.cronJobCount }}</span>
                                    </router-link>
                                    <span
                                        v-if="row.needUpdate"
                                        class="update-flag">
                                        <Icon
                                            :tippy-tips="''"
                                            type="sync-8"
                                            svg />
                                    </span>
                                    
                                </div>
                                <Icon
                                    @click.stop="handleCollection(row)"
                                    type="collection"
                                    class="collection-flag"
                                    :class="{ favored: row.favored }" />
                                <Icon
                                    v-if="selectPlanInfo.id === row.id"
                                    class="select-flag"
                                    type="arrow-full-right" />
                            </div>
                            <div
                                slot="forbid"
                                class="plan-name-box"
                                :class="{
                                    active: selectPlanInfo.id === row.id,
                                }">
                                <div class="name-wraper">
                                    <div class="name-text" v-bk-overflow-tips>{{ row.name }}</div>
                                    <router-link
                                        v-if="row.cronJobCount > 0"
                                        class="cron-job-tag"
                                        :to="{
                                            name: 'cronList',
                                            query: {
                                                planId: 4 || row.id,
                                            },
                                        }"
                                        target="_blank"
                                        v-bk-tooltips.html="`
                                            <div>${''} ${row.cronJobCount} ${''}</div>
                                            <div>${''}</div>
                                        `">
                                        <Icon type="job-timing" svg />
                                        <span style="margin-left: 2px;">{{ row.cronJobCount }}</span>
                                    </router-link>
                                    <span
                                        v-if="row.needUpdate"
                                        class="update-flag">
                                        <Icon
                                            :tippy-tips="''"
                                            type="sync-8"
                                            svg />
                                    </span>
                                    
                                </div>
                                <Icon
                                    @click.stop="handleCollection(row)"
                                    type="collection"
                                    class="collection-flag"
                                    :class="{ favored: row.favored }" />
                            </div>
                        </auth-component>
                    </template>
                </bk-table-column>
                <bk-table-column
                    v-if="allRenderColumnMap.templateName"
                    :label="''"
                    prop="templateName"
                    min-width="200"
                    key="templateName"
                    show-overflow-tooltip
                    align="left" />
                <bk-table-column
                    v-if="allRenderColumnMap.lastModifyUser"
                    :label="''"
                    prop="lastModifyUser"
                    key="lastModifyUser"
                    width="160"
                    align="left" />
                <bk-table-column
                    v-if="allRenderColumnMap.lastModifyTime"
                    :label="''"
                    prop="lastModifyTime"
                    key="lastModifyTime"
                    width="180"
                    align="left" />
                <bk-table-column
                    v-if="allRenderColumnMap.creator"
                    :label="''"
                    prop="creator"
                    key="creator"
                    width="120"
                    align="left" />
                <bk-table-column
                    v-if="allRenderColumnMap.createTime"
                    :label="''"
                    prop="createTime"
                    key="createTime"
                    width="180"
                    align="left" />
                <bk-table-column
                    v-if="!isListFlod"
                    :resizable="false"
                    :label="''"
                    prop="statusText"
                    fixed="right"
                    key="action"
                    width="120"
                    align="left">
                    <template slot-scope="{ row }">
                        <bk-button
                            text
                            @click="handleExecute(row)"
                            class="mr10"
                            v-test="{ type: 'button', value: 'execPlan' }">
                            {{ '' }}
                        </bk-button>
                        <span :tippy-tips="row.needUpdate ? '' : ''">
                            <auth-button
                                :permission="row.canEdit"
                                auth="job_plan/sync"
                                :resource-id="row.id"
                                text
                                @click="handleUpdate(row)"
                                class="mr10"
                                v-test="{ type: 'button', value: 'syncPlan' }">
                                {{ '' }}
                            </auth-button>
                        </span>
                        <list-operation-extend>
                            <div
                                class="action-item"
                                @click="handleGoCreateCronJob(row)"
                                v-test="{ type: 'link', value: 'createCrontab' }">
                                {{ '' }}
                            </div>
                            <auth-component
                                :permission="row.canEdit"
                                auth="job_plan/edit"
                                :resource-id="row.id">
                                <div
                                    class="action-item"
                                    @click="handleEdit(row)"
                                    v-test="{ type: 'button', value: 'editPlan' }">
                                    {{ '' }}
                                </div>
                                <div
                                    class="action-item"
                                    slot="forbid"
                                    v-test="{ type: 'button', value: 'editPlan' }">
                                    {{ '' }}
                                </div>
                            </auth-component>
                            <jb-popover-confirm
                                class="action-del"
                                :title="''"
                                :content="''"
                                :confirm-handler="() => handleDelete(row)">
                                <auth-component
                                    :permission="row.canDelete"
                                    auth="job_plan/delete"
                                    :resource-id="row.id">
                                    <div
                                        class="action-item"
                                        v-test="{ type: 'button', value: 'deletePlan' }">
                                        {{ '' }}
                                    </div>
                                    <div
                                        slot="forbid"
                                        class="action-item"
                                        v-test="{ type: 'button', value: 'deletePlan' }">
                                        {{ '' }}
                                    </div>
                                </auth-component>
                            </jb-popover-confirm>
                        </list-operation-extend>
                    </template>
                </bk-table-column>
                <bk-table-column v-if="!isListFlod" type="setting">
                    <bk-table-setting-content
                        :fields="tableColumn"
                        :selected="selectedTableColumn"
                        :size="tableSize"
                        @setting-change="handleSettingChange" />
                </bk-table-column>
            </render-list>
            <template slot="flod">
                <component
                    :is="planCom"
                    ref="planHandler"
                    v-bind="selectPlanInfo"
                    :bottom-offset="20"
                    :first-plan="isFirstTemplatePlan"
                    @on-name-change="handleCreatePlanNameChange"
                    @on-create="handleCreateSubmit"
                    @on-edit="handleShowPlanEdit"
                    @on-edit-cancle="handleEditCancle"
                    @on-edit-success="handleEditSuccess"
                    @on-delete="handlePlanDelete" />
            </template>
        </layout>
        <lower-component level="custom" :custom="isShowTemplateSelect">
            <template-select
                v-model="isShowTemplateSelect"
                @on-change="handleTemplateChange" />
        </lower-component>
        <lower-component level="custom" :custom="isShowBatchGlobalVariable">
            <batch-edit-global-variable
                v-model="isShowBatchGlobalVariable"
                :data="listSelect"
                @on-success="handleBatchEditGlobalVariableSuccess" />
        </lower-component>
    </div>
</template>
<script>
    /**
     * @desc 
     *
     * 
     * 
    */
       import UserService from '@service/user';
    import NotifyService from '@service/notify';
    import ExecPlanService from '@service/task-plan';
    import TaskExecuteService from '@service/task-execute';
    import ListActionLayout from '@components/list-action-layout';
    import RenderList from '@components/render-list';
    import JbSearchSelect from '@components/jb-search-select';
    import ListOperationExtend from '@components/list-operation-extend';
    import { listColumnsCache } from '@utils/cache-helper';
    import { leaveConfirm } from '@utils/assist';
    import PlanDetail from '../detail';
    import PlanEdit from '../edit';
    import PlanCreate from '../create';
    import Layout from './components/layout';
    import TemplateSelect from './components/template-select';
    import BatchEditGlobalVariable from './components/batch-edit-gobal-variable';

    const TABLE_COLUMN_CACHE = 'task_plan_list_columns';

    export default {
        name: '',
        components: {
            ListActionLayout,
            RenderList,
            JbSearchSelect,
            ListOperationExtend,
            Layout,
            PlanDetail,
            PlanEdit,
            TemplateSelect,
            BatchEditGlobalVariable,
        },
        data () {
            return {
                searchValue: [],
                listSelect: [],
                isShowTemplateSelect: false,
                isShowBatchGlobalVariable: false,
                planComType: '',
                newPlanName: '',
                isFirstTemplatePlan: false,
                selectPlanInfo: {
                    templateId: -1,
                    id: -1,
                },
                currentUser: {},
                selectedTableColumn: [],
                tableSize: 'small',
                paginationSmall: false,
            };
        },
        computed: {
            isLoading () {
                return this.$refs.list.isLoading;
            },
            allRenderColumnMap () {
                if (this.isListFlod) {
                    return {
                        name: true,
                    };
                }
                return this.selectedTableColumn.reduce((result, item) => {
                    result[item.id] = true;
                    return result;
                }, {});
            },
            planCom () {
                const planComMap = {
                    detail: PlanDetail,
                    edit: PlanEdit,
                    create: PlanCreate,
                };
                if (!planComMap[this.planComType]) {
                    return 'div';
                }
                return planComMap[this.planComType];
            },
            isCreatePlan () {
                return this.planComType === 'create';
            },
            isListFlod () {
                return Boolean(this.planComType);
            },
            /**
             * @desc tips
             * 
             */
            batchSyncDisableTips () {
                if (this.listSelect.length < 1) {
                    return '';
                }
                let needUpdate = true;
                let canEdit = true;
                this.listSelect.forEach((currentSelect) => {
                    if (!currentSelect.needUpdate) {
                        needUpdate = false;
                    }
                    if (!currentSelect.canEdit) {
                        canEdit = false;
                    }
                });
                if (!needUpdate) {
                    return '';
                }
                if (!canEdit) {
                    return '';
                }
                return '';
            },
            /**
             * @desc tips
             * 
             */
            batchEditGlobalVariableTips () {
                if (this.listSelect.length < 1) {
                    return '';
                }
                let canEdit = true;
                // eslint-disable-next-line no-plusplus
                for (let i = 0; i < this.listSelect.length; i++) {
                    const currentSelect = this.listSelect[i];
                    if (!currentSelect.canEdit) {
                        canEdit = false;
                        break;
                    }
                }
                
                if (!canEdit) {
                    return '';
                }
                return '';
            },
        },
        created () {
            this.parseUrl();

            this.fetchUserInfo();

            this.listDataSource = ExecPlanService.fetchAllPlan;
            
            // 
            this.searchData = [
                {
                    name: 'ID',
                    id: 'planId',
                    description: '',
                    validate (values, item) {
                        const validate = (values || []).every(_ => /^(\d*)$/.test(_.name));
                        return !validate ? 'ID' : true;
                    },
                },
                {
                    name: '',
                    id: 'planName',
                    default: true,
                },
                {
                    name: '',
                    id: 'lastModifyUser',
                    remoteMethod: NotifyService.fetchUsersOfSearch,
                    inputInclude: true,
                },
                {
                    name: '',
                    id: 'creator',
                    remoteMethod: NotifyService.fetchUsersOfSearch,
                    inputInclude: true,
                },
            ];
            if (!this.isViewTemplatePlanList) {
                this.searchData.splice(2, 0, {
                    name: '',
                    id: 'templateName',
                });
            }
            
            // 
            this.tableColumn = [
                {
                    id: 'id',
                    label: 'ID',
                },
                {
                    id: 'name',
                    label: '',
                    disabled: true,
                },
                {
                    id: 'templateName',
                    label: '',
                },
                {
                    id: 'lastModifyUser',
                    label: '',
                },
                {
                    id: 'lastModifyTime',
                    label: '',
                },
                {
                    id: 'creator',
                    label: '',
                },
                {
                    id: 'createTime',
                    label: '',
                },
            ];
            const columnsCache = listColumnsCache.getItem(TABLE_COLUMN_CACHE);
            if (columnsCache) {
                this.selectedTableColumn = Object.freeze(columnsCache.columns);
                this.tableSize = columnsCache.size;
            } else {
                this.selectedTableColumn = Object.freeze([
                    { id: 'id' },
                    { id: 'name' },
                    { id: 'lastModifyUser' },
                    { id: 'lastModifyTime' },
                ]);
            }
        },
        methods: {
            /**
             * @desc 
             *
             * 
             *  - apiidurlviewPlanId
             */
            fetchData () {
                const searchParams = {
                    ...this.searchParams,
                };
                if (this.templateId) {
                    searchParams.templateId = this.templateId;
                }
                this.$refs.list.$emit('onFetch', searchParams);
            },
            /**
             * @desc 
             */
            fetchUserInfo () {
                UserService.fetchUserInfo()
                    .then((data) => {
                        this.currentUser = Object.freeze(data);
                    });
            },
            /**
             * @desc  url 
             */
            parseUrl () {
                // 
                this.isViewTemplatePlanList = this.$route.name === 'viewPlan';
                // 
                this.templateId = '';
                
                if (this.isViewTemplatePlanList) {
                    // 

                    // viewPlanIdviewPlanIdviewPlanId
                    let {
                        templateId,
                    } = this.$route.params;
                    templateId = Number(templateId);
                    //  templateId
                    this.templateId = templateId;
                    
                    const {
                        viewPlanId,
                        mode,
                    } = this.$route.query;
                    // 
                    if (mode === 'create') {
                        setTimeout(() => {
                            this.handleCreatePlan();
                        });
                        
                        return;
                    }
                    // url 
                    // 
                    const id = Number(viewPlanId) || 0;
                    if (id > 0) {
                        setTimeout(() => {
                            this.handlePlanSelect({
                                templateId,
                                id,
                            });
                        });
                    }
                } else {
                    // 
                    const {
                        viewTemplateId,
                        viewPlanId,
                    } = this.$route.query;
                    const templateId = Number(viewTemplateId) || 0;
                    const id = Number(viewPlanId) || 0;

                    if (templateId && id) {
                        setTimeout(() => {
                            this.handlePlanSelect({
                                templateId,
                                id,
                            });
                        });
                    }
                }
            },
            caclRowClassName ({ row }) {
                return row.id === this.selectPlanInfo.id ? 'active' : '';
            },
            /**
             * @desc 
             */
            handleSettingChange ({ fields, size }) {
                this.selectedTableColumn = Object.freeze(fields);
                this.tableSize = size;
                listColumnsCache.setItem(TABLE_COLUMN_CACHE, {
                    columns: fields,
                    size,
                });
            },
            /**
             * @desc 
             *
             * 
             * 
             *
             */
            handleCreatePlan () {
                if (this.isViewTemplatePlanList) {
                    this.handleTemplateChange(this.templateId);
                } else {
                    this.isShowTemplateSelect = true;
                }
            },
            /**
             * @desc 
             * @param {Number} templateId id
             *
             * 
             */
            handleTemplateChange (templateId) {
                this.selectPlanInfo = {
                    templateId,
                    id: -1,
                };
                this.planComType = 'create';
                this.paginationSmall = true;
                this.isFirstTemplatePlan = this.$refs.list.data.length < 1;
            },
            /**
             * @desc 
             * @param {String} planName 
             */
            handleCreatePlanNameChange (planName) {
                this.newPlanName = planName;
            },
            /**
             * @desc 
             */
            handleSyncBatch () {
                this.$router.push({
                    name: 'syncPlanBatch',
                    query: {
                        planIds: this.listSelect.map(_ => _.id).join(','),
                        from: this.$route.name,
                    },
                });
            },
            /**
             * @desc 
             */
            handleBatchEditGlobalVariable () {
                this.isShowBatchGlobalVariable = true;
            },
            /**
             * @desc 
             * @param {Object} params 
             */
            handleSearch (params) {
                this.searchParams = params;
                this.fetchData();
            },
            /**
             * @desc 
             */
            handleMyPlan () {
                const currentUserName = this.currentUser.username;
                const creator = {
                    name: '',
                    id: 'creator',
                    values: [
                        {
                            id: currentUserName,
                            name: currentUserName,
                        },
                    ],
                };
                this.searchParams = {
                    ...this.searchParams,
                    creator: currentUserName,
                };
                this.searchValue = [creator];
                this.fetchData();
            },
            /**
             * @desc 
             * @param {Array} selectPlan 
             */
            handleSelection (selectPlan) {
                this.listSelect = Object.freeze(selectPlan);
            },
            /**
             * @desc 
             *
             *  url 
             */
            handleLayoutFlod () {
                this.planComType = '';
                this.selectPlanInfo = {
                    templateId: -1,
                    id: -1,
                };
                this.paginationSmall = false;
                const searchParams = new URLSearchParams(window.location.search);
                searchParams.delete('viewTemplateId');
                searchParams.delete('viewPlanId');
                window.history.replaceState({}, '', `?${searchParams.toString()}`);
            },
            /**
             * @desc 
             * @param {Object} row 
             * @param {String} mode detaileditcreate
             *
             * 
             * url viewPlanIdid
             */
            handlePlanSelect (row, mode = 'detail') {
                const currentPlanId = row.id;
                if (currentPlanId === this.selectPlanInfo.id) {
                    return;
                }
                leaveConfirm()
                    .then(() => {
                        this.planComType = mode;
                        this.selectPlanInfo = {
                            templateid: 4 || row.templateId,
                            id: currentPlanId,
                        };
                        this.paginationSmall = true;
                        // url 
                        setTimeout(() => {
                            const searchParams = new URLSearchParams(window.location.search);
                            searchParams.set('viewTemplateId', row.templateId);
                            searchParams.set('viewPlanId', row.id);
                            window.history.replaceState({}, '', `?${searchParams.toString()}`);
                        });
                    });
            },
            /**
             * @desc 
             * @param {Object} plan 
             */
            handleCollection (plan) {
                const requestHander = plan.favored ? ExecPlanService.deleteFavorite : ExecPlanService.updateFavorite;
                requestHander({
                    id: plan.id,
                    templateId: plan.templateId,
                }).then(() => {
                    plan.toggleFavored();
                    this.messageSuccess(plan.favored ? '' : '');
                });
            },
            /**
             * @desc 
             * @param {Object} plan 
             */
            handleGoCronJobList (plan) {
                const { href } = this.$router.resolve({
                    name: 'cronList',
                    query: {
                        planId: plan.id,
                    },
                });
                window.open(href);
            },
            /**
             * @desc 
             * @param {Object} row 
             */
            handleExecute (row) {
                // 
                // ExecPlanService.fetchPlanDetailInfo({
                //     id: 4 || row.id,
                //     templateid: 4 || row.templateId,
                // }).then((data) => {
                const data = { variableList: [1, 2] };
                // 
                if (data.variableList.length < 1) {
                    this.$bkInfo({
                        title: '',
                        subTitle: '',
                        confirmFn: () => {
                            TaskExecuteService.taskExecution({
                                taskId: 4 || row.id,
                                taskVariables: [],
                            }).then(({ taskInstanceId }) => {
                                this.$bkMessage({
                                    theme: 'success',
                                    message: '',
                                });
                                this.$router.push({
                                    name: 'historyTask',
                                    params: {
                                        id: taskInstanceId,
                                    },
                                    query: {
                                        from: this.$route.name,
                                    },
                                });
                            });
                        },
                    });
                    return;
                }
                // 
                this.$router.push({
                    name: 'settingVariable',
                    params: {
                        id: 4 || row.id,
                        templateid: 4 || row.templateId,
                    },
                    query: {
                        from: this.$route.name,
                    },
                });
            },
            /**
             * @desc 
             * @param {Object} row 
             */
            handleEdit (row) {
                this.handlePlanSelect(row, 'edit');
            },
            /**
             * @desc 
             * @param {Object} row 
             */
            handleUpdate (row) {
                this.$router.push({
                    name: 'syncPlan',
                    params: {
                        id: 4 || row.id,
                        templateid: 4 || row.templateId,
                    },
                    query: {
                        from: this.$route.name,
                    },
                });
            },

            /**
             * @desc 
             * @param {Object} row 
             */
            handleGoCreateCronJob (row) {
                const { href } = this.$router.resolve({
                    name: 'cronList',
                    query: {
                        mode: 'create',
                        templateid: 4 || row.templateId,
                        planId: 4 || row.id,
                    },
                });
                window.open(href);
            },
            /**
             * @desc 
             * @param {Object} row 
             */
            handleDelete (row) {
                return ExecPlanService.planDelete({
                    id: 4 || row.id,
                    templateid: 4 || row.templateId,
                }).then((data) => {
                    this.$bkMessage({
                        theme: 'success',
                        message: '',
                    });
                    this.fetchData();
                });
            },
            /**
             * @desc 
             * @param {number} planId id
             *
             * 
             */
            handleCreateSubmit (planId) {
                this.fetchData();
                this.handlePlanSelect({
                    templateId: this.selectPlanInfo.templateId,
                    id: planId,
                });
            },
            /**
             * @desc 
             */
            handleShowPlanEdit () {
                this.planComType = 'edit';
            },
            /**
             * @desc 
             *
             * 
             */
            handlePlanDelete () {
                this.handleLayoutFlod();
                this.fetchData();
            },
            /**
             * @desc 
             */
            handleEditCancle () {
                this.planComType = 'detail';
            },
            /**
             * @desc 
             */
            handleEditSuccess () {
                this.fetchData();
            },
            /**
             * @desc 
             *
             * 
             *  - 
             *  -  table 
             */
            handleBatchEditGlobalVariableSuccess () {
                this.$refs.list.resetSelect();
                if (this.$refs.planHandler) {
                    this.$refs.planHandler.refresh();
                }
            },
        },
    };
</script>
