<template>
    <div class="server-file-edit-box" v-bkloading="{ isLoading }">
        <table>
            <thead>
                <th>{{ '' }}</th>
                <th>{{ '' }}</th>
                <th>{{ 'Agent ' }}</th>
                <th>{{ '' }}</th>
                <th>{{ '' }}</th>
            </thead>
            <tbody>
                <tr v-for="(row, index) in serverFileList" :key="index">
                    <td>
                        <edit-file-path
                            :value="row.fileLocation"
                            @on-change="value => handleFilePathEdit(value, index)" />
                    </td>
                    <td>
                        <!--  -->
                        <div
                            v-if="row.isVar"
                            class="global-variable-render-box"
                            :class="{ 'value-error': !row.host.variable }"
                            style="margin-left: -10px;">
                            <bk-select
                                class="variable-edit-select"
                                :value="row.host.variable"
                                :clearable="false"
                                @change="value => handleVariableChange(value, index)"
                                searchable>
                                <bk-option
                                    v-for="(option, variableIndex) in variable"
                                    :key="variableIndex"
                                    :id="option.name"
                                    :name="option.name" />
                            </bk-select>
                            <Icon
                                v-if="!row.host.variable"
                                type="info"
                                class="error-tips"
                                v-bk-tooltips="''" />
                        </div>
                        <!--  -->
                        <div
                            v-else
                            class="server-edit-btn"
                            @click="handleHostEdit(index)"
                            v-html="row.serverDesc" />
                    </td>
                    <td>
                        <!--  -->
                        <render-server-agent
                            v-if="!row.isVar"
                            :title="`${'-'}`"
                            :host-node-info="row.host.hostNodeInfo"
                            key="host"
                            :separator="agentSeparator" />
                        <!--  -->
                        <render-server-agent
                            v-else
                            :title="`${''} - ${row.host.variable}`"
                            :host-node-info="findVariableValue(row.serverDesc)"
                            :key="row.serverDesc"
                            :separator="agentSeparator" />
                    </td>
                    <td>
                        <account-select
                            class="account-add-btn"
                            :value="row.account"
                            type="system"
                            @change="value => handleAccountChange(value, index)" />
                    </td>
                    <td>
                        <bk-button text @click="handlerRemove(index)">
                            {{ '' }}
                        </bk-button>
                    </td>
                </tr>
            </tbody>
            <component
                :is="addCom"
                key="add"
                v-bind="$attrs"
                :data="serverFileList"
                :account="accountList"
                :variable="variable"
                @on-change="handleAddSave"
                @on-cancel="handleAddCancel" />
        </table>
        <lower-component level="custom" :custom="isShowChooseIp">
            <choose-ip
                v-model="isShowChooseIp"
                required
                :host-node-info="currentHost"
                @on-change="handleHostChange" />
        </lower-component>
    </div>
</template>
<script>
    import { mapMutations } from 'vuex';
    import AccountManageService from '@service/account-manage';
    import TaskHostNodeModel from '@model/task-host-node';
    import ChooseIp from '@components/choose-ip';
    import RenderServerAgent from '@components/render-server-agent';
    import AccountSelect from '@components/account-select';
    import AddOnlyHost from './only-host';
    import AddHostAndVariable from './host-and-variable';
    import EditFilePath from '../../components/edit-file-path';

    export default {
        name: 'SourceFileServer',
        components: {
            ChooseIp,
            RenderServerAgent,
            AccountSelect,
            AddOnlyHost,
            AddHostAndVariable,
            EditFilePath,
        },
        inheritAttrs: false,
        props: {
            data: {
                type: Array,
                required: true,
            },
            mode: {
                type: String,
                default: '',
            },
            variable: {
                type: Array,
                default: () => [],
            },
        },
        data () {
            return {
                isLoading: true,
                serverFileList: [{},{},{}],
                currentHost: {},
                accountList: [{},{},{}],
                isShowChooseIp: false,
                currentIndex: 0,
            };
        },
        computed: {
            addCom () {
                if (this.isLoading) {
                    return 'div';
                }
                if (this.mode === 'onlyHost') {
                    return AddOnlyHost;
                }
                return AddHostAndVariable;
            },
            agentSeparator () {
                if (this.mode === 'onlyHost') {
                    return '';
                }
                return '\n';
            },
        },
        watch: {
            data: {
                handler (newData) {
                    if (this.innerChange) {
                        this.innerChange = false;
                        return;
                    }
                    this.serverFileList = newData;
                },
                immediate: true,
            },
        },
        created () {
            this.fetchAccount();
            this.editNewSourceFile(false);
        },
        methods: {
            ...mapMutations('distroFile', [
                'editNewSourceFile',
            ]),
            /**
             * @desc 
             */
            fetchAccount () {
                AccountManageService.fetchAccountWhole({
                    category: 1,
                }).then((data) => {
                    this.accountList = data;
                })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            /**
             * @desc 
             * @param {String} variableName 
             */
            findVariableValue (variableName) {
                const curVariable = this.variable.find(item => item.name === variableName);
                if (!curVariable) {
                    const {
                        hostNodeInfo,
                    } = new TaskHostNodeModel({});
                    return hostNodeInfo;
                }
                return curVariable.defaultTargetValue.hostNodeInfo;
            },
            /**
             * @desc 
             */
            triggerChange () {
                this.innerChange = true;
                this.$emit('on-change', [
                    ...this.serverFileList,
                ]);
            },
            /**
             * @desc 
             * @param {Array} fileLocation 
             * @param {Number} index 
             */
            handleFilePathEdit (fileLocation, index) {
                this.serverFileList[index].fileLocation = fileLocation;
                this.triggerChange();
            },
            /**
             * @desc ip
             * @param {Number} index 
             */
            handleHostEdit (index) {
                this.isShowChooseIp = true;
                this.currentIndex = index;
                this.currentHost = this.data[index].host.hostNodeInfo;
            },
            /**
             * @desc 
             * @param {Object} hostNodeInfo 
             */
            handleHostChange (hostNodeInfo) {
                this.serverFileList[this.currentIndex].host.hostNodeInfo = hostNodeInfo;
                this.triggerChange();
            },
            /**
             * @desc 
             * @param {Array} variable 
             * @param {Number} index 
             */
            handleVariableChange (variable, index) {
                this.serverFileList[index].host.variable = variable;
                this.triggerChange();
            },
            /**
             * @desc 
             * @param {Number} index 
             */
            handleEditAccount (index) {
                this.serverFileList[index].isEditAccount = true;
                this.editAccountIndex = index;
                this.triggerChange();
            },
            /**
             * @desc 
             * @param {Number} account id
             * @param {Number} index 
             */
            handleAccountChange (account, index) {
                const serverFile = this.serverFileList[index];
                serverFile.account = account;
                this.triggerChange();
            },
            /**
             * @desc 
             * @param {Number} index 
             */
            handlerRemove (index) {
                this.serverFileList.splice(index, 1);
                this.triggerChange();
            },
            /**
             * @desc 
             * @param {Object} serverFile 
             */
            handleAddSave (serverFile) {
                this.serverFileList.push(serverFile);
                this.triggerChange();
            },
            /**
             * @desc 
             */
            handleAddCancel () {
                if (this.serverFileList.length < 1) {
                    this.$emit('on-close');
                }
            },
        },
    };
</script>
<style lang='postcss'>
    .server-file-edit-box {
        .global-variable-render-box {
            position: relative;
            border: 1px solid transparent;
            border-radius: 2px;

            &.value-error {
                border-color: #ea3636;
            }

            .error-tips {
                position: absolute;
                top: 0;
                right: 0;
                z-index: 1999;
                display: flex;
                height: 30px;
                padding: 0 6px;
                font-size: 16px;
                color: #ea3636;
                cursor: pointer;
                align-items: center;
            }
        }

        .variable-edit-select,
        .account-add-btn {
            &:hover,
            &.is-focus {
                .bk-select-angle {
                    display: block;
                }
            }

            .bk-select-angle {
                display: none;
                font-size: 20px;
            }
        }

        .account-add-btn,
        .server-edit-btn,
        .server-add-only-host-btn {
            margin-left: -10px;
        }

        .server-add-btn,
        .server-edit-btn,
        .server-add-host {
            cursor: pointer;
            border-radius: 2px;

            &:hover {
                background: #f0f1f5;
            }
        }

        .server-add-btn,
        .server-add-host {
            height: 30px;
            line-height: 30px;
        }

        .server-edit-btn {
            min-height: 30px;
            padding: 5px 10px;
            line-height: 20px;
        }

        .server-add-btn {
            display: flex;
            height: 30px;
            margin-left: -10px;
            font-size: 12px;
            cursor: pointer;
            background: #f7f8fa;
            border-radius: 2px;
            align-items: center;

            .server-type-select {
                flex: 0 0 auto;
            }

            .line {
                height: 16px;
                background: #dcdee5;
                flex: 0 0 1px;
            }

            .server-add-variable {
                flex: 1;
            }

            .server-add-host {
                display: flex;
                height: 30px;
                font-size: 12px;
                border-radius: 2px;
                flex: 1;
                align-items: center;
                justify-content: center;
            }
        }

        .server-add-only-host-btn {
            display: flex;
            height: 30px;
            padding: 0 10px;
            font-size: 12px;
            cursor: pointer;
            background: #f7f8fa;
            border-radius: 2px;
            align-items: center;
            justify-content: center;

            &:hover {
                color: #979ba5;
                background: #f0f1f5;
            }

            .add-flag {
                margin-right: 6px;
                margin-bottom: 2px;
            }
        }

        .create-server-file {
            tr {
                td {
                    border-top: 1px solid #dcdee5;
                }
            }
        }

        .bk-select {
            height: 30px;
            line-height: 30px;
            border: none;

            &:hover {
                background: #f0f1f5;
            }

            .bk-select-name {
                height: 30px;
                padding-right: 16px;
                padding-left: 10px;
                line-height: 30px;
            }
        }
    }
</style>
