<template>
    <div
        class="server-panel"
        :class="classes">
        <div
            v-if="needRender"
            class="server-result-wraper">
            <bk-collapse v-model="activePanel">
                <server-host
                    v-if="hostList.length > 0 || renderWithEmpty"
                    ref="host"
                    :data="hostList"
                    :editable="editable"
                    :diff="hostDiff"
                    @on-change="handleHostChange" />
                <server-node
                    v-if="nodeInfo.length > 0 || renderWithEmpty"
                    ref="node"
                    :data="nodeInfo"
                    :editable="editable"
                    :diff="nodeDiff"
                    @on-view="handleView"
                    @on-change="handleNodeChange" />
                <server-group
                    v-if="groupList.length > 0 || renderWithEmpty"
                    ref="group"
                    :data="groupList"
                    :editable="editable"
                    :diff="groupDiff"
                    @on-view="handleView"
                    @on-change="handleGroupChange" />
            </bk-collapse>
        </div>
        <lower-component level="custom" :custom="showDetail">
            <host-detail
                v-model="showDetail"
                :data="viewInfo"
                :append="hostDetailAppend" />
        </lower-component>
        <lower-component level="custom" :custom="searchMode">
            <host-search
                v-show="searchMode"
                :data="searchData"
                :editable="editable"
                @on-change="handleSearchChange" />
        </lower-component>
    </div>
</template>
<script>
    import { encodeRegexp } from '@utils/assist';
    import TaskHostNodeModel from '@model/task-host-node';
    import ServerNode from './view/node';
    import ServerHost from './view/host';
    import ServerGroup from './view/group';
    import HostDetail from './view/host-detail';
    import HostSearch from './view/host-search';
    import { generateHostRealId } from './components/utils';

    const addCollapsePanel = (target, name) => {
        if (target.length > 0) {
            return;
        }
        if (!target.includes(name)) {
            target.push(name);
        }
    };

    export default {
        name: 'ServerPanel',
        components: {
            ServerNode,
            ServerHost,
            ServerGroup,
            HostDetail,
            HostSearch,
        },
        props: {
            hostNodeInfo: {
                type: Object,
                default: () => new TaskHostNodeModel(),
            },
            // 
            editable: {
                type: Boolean,
                default: false,
            },
            // 
            renderWithEmpty: {
                type: Boolean,
                default: false,
            },
            // 
            hostDetailAppend: {
                type: Function,
                default: () => document.querySelector('body'),
            },
            // 
            searchMode: {
                type: Boolean,
                default: false,
            },
            // 
            searchData: {
                type: Array,
                default: () => [],
            },
            nodeDiff: {
                type: Object,
                default: () => ({}),
            },
            hostDiff: {
                type: Object,
                default: () => ({}),
            },
            groupDiff: {
                type: Object,
                default: () => ({}),
            },
        },
        data () {
            return {
                activePanel: [],
                showDetail: false,
                viewInfo: {},
                nodeInfo: [],
                hostList: [{},{},{}],
                groupList: [{},{},{}],
                requestQueue: [],
            };
        },
        computed: {
            needRender () {
                if (this.renderWithEmpty) {
                    return true;
                }
                return this.nodeInfo.length || this.hostList.length || this.groupList.length;
            },
            classes () {
                const stack = [];
                if (this.searchMode) {
                    stack.push('show-search');
                }
                return stack;
            },
        },
        watch: {
            hostNodeInfo: {
                handler (hostNodeInfo) {
                    const { dynamicGroupList, ipList, topoNodeList } = hostNodeInfo;
                    this.hostList = Object.freeze(ipList);
                    if (ipList.length > 0) {
                        addCollapsePanel(this.activePanel, 'host');
                    }
                    this.nodeInfo = Object.freeze(topoNodeList);
                    if (topoNodeList.length > 0) {
                        addCollapsePanel(this.activePanel, 'node');
                    }
                    this.groupList = Object.freeze(dynamicGroupList);
                    if (dynamicGroupList.length > 0) {
                        addCollapsePanel(this.activePanel, 'group');
                    }
                },
                immediate: true,
            },
        },
        methods: {
            /**
             * @desc api
             * @param {Boolean} returnErrorIP true: ipfalse: ip
             *
             * ip()ip
             */
            getAllIP (returnErrorIP = false) {
                const allHostList = [];
                if (this.$refs.host) {
                    allHostList.push(...this.$refs.host.getAllHost());
                }
                if (this.$refs.node) {
                    allHostList.push(...this.$refs.node.getAllHost());
                }
                if (this.$refs.group) {
                    allHostList.push(...this.$refs.group.getAllHost());
                }
                const ipMap = {};
                const errorIPMap = {};
                allHostList.forEach((host) => {
                    ipMap[host.ip] = 1;
                    if (host.alive !== 1) {
                        errorIPMap[host.ip] = 1;
                    }
                });
                
                // 
                if (returnErrorIP) {
                    return Object.keys(errorIPMap);
                }
                return Object.keys(ipMap);
            },
            /**
             * @desc api
             * @param {String} filter 
             *
             * ip + 
             */
            getAllHost (filter = '') {
                if (filter.trim() === '') {
                    return this.$refs.host.getAllHost();
                }
                if (!/^[0-9.]+$/g.test(filter)) {
                    return [];
                }
                
                const filterReg = new RegExp(`${encodeRegexp(filter.trim())}`);
                const hostMap = {};
                
                if (this.$refs.host) {
                    this.$refs.host.getAllHost().forEach((host) => {
                        if (filterReg.test(host.ip)) {
                            hostMap[generateHostRealId(host)] = host;
                        }
                    });
                }
                
                return Object.values(hostMap);
            },
            /**
             * @desc 
             */
            removeAllInvalidHost () {
                this.$refs.host && this.$refs.host.removeAllInvalidHost();
                this.$refs.node && this.$refs.node.removeAllInvalidHost();
                this.$refs.group && this.$refs.group.removeAllInvalidHost();
            },
            /**
             * @desc 
             *
             * ip + 
             */
            refresh () {
                this.$refs.host && this.$refs.host.refresh();
                this.$refs.node && this.$refs.node.refresh();
                this.$refs.group && this.$refs.group.refresh();
            },
            /**
             * @desc 
             */
            trigger () {
                this.$emit('on-change', {
                    ipList: this.hostList,
                    topoNodeList: this.nodeInfo,
                    dynamicGroupList: this.groupList,
                });
            },
            /**
             * @desc loading
             * @param {Boolean} loading loading
             */
            handleLoading (loading) {
                if (loading) {
                    this.requestQueue.push(true);
                } else {
                    this.requestQueue.pop();
                }
            },
            /**
             * @desc 
             * @param {Boolean} payload 
             */
            handleView (payload) {
                this.showDetail = true;
                this.viewInfo = Object.freeze(payload);
            },
            /**
             * @desc 
             * @param {Array} hostList 
             */
            handleHostChange (hostList) {
                this.hostList = Object.freeze(hostList);
                this.trigger();
            },
            /**
             * @desc 
             * @param {Array} nodeInfo 
             */
            handleNodeChange (nodeInfo) {
                this.nodeInfo = Object.freeze(nodeInfo);
                this.trigger();
            },
            /**
             * @desc 
             * @param {Array} groupList 
             */
            handleGroupChange (groupList) {
                this.groupList = Object.freeze(groupList);
                this.trigger();
            },
            /**
             * @desc 
             * @param {Array} removeHostList 
             */
            handleSearchChange (removeHostList) {
                const removeHostMap = {};
                removeHostList.forEach((item) => {
                    removeHostMap[item.realId] = true;
                });

                const result = [];
                this.$refs.host.list.forEach((currentHost) => {
                    if (!removeHostMap[currentHost.realId]) {
                        result.push(currentHost);
                    }
                });
                this.hostList = Object.freeze(result);
                this.trigger();
            },
        },
    };
</script>
<style lang='postcss'>
    .server-panel {
        position: relative;
        z-index: 1;
        width: 100%;
        background: #fff;

        &.show-search {
            .server-result-wraper {
                display: none;
            }
        }

        .bk-collapse-item-header {
            display: flex;
            align-items: center;
            padding-left: 23px;

            .panel-title {
                padding-left: 23px;
            }
        }

        .server-result-wraper {
            height: 100%;
        }

        .choose-ip-host-table {
            thead {
                th {
                    background: #fff;
                }
            }

            .invalid-list {
                td {
                    color: #c4c6cc;
                }
            }

            tbody:last-child {
                tr:last-child {
                    td {
                        border-bottom: none;
                    }
                }
            }
        }

        /* diff  */
        .choose-ip-host-table { /* stylelint-disable-line */
            tr.delete {
                * {
                    color: #c4c6cc !important;
                    text-decoration: line-through;
                }
            }

            tr.new {
                td:first-child {
                    position: relative;

                    &::before {
                        position: absolute;
                        top: 50%;
                        width: 24px;
                        height: 16px;
                        margin-left: -32px;
                        font-size: 12px;
                        line-height: 16px;
                        color: #fff;
                        text-align: center;
                        background: #f0c581;
                        border-radius: 2px;
                        content: "new";
                        transform: translateY(-50%);
                    }
                }
            }
        }
    }
</style>
