<template>
    <layout
        v-bind="$attrs"
        class="task-plan-edit-box"
        :plan-name="name"
        :loading="isLoading">
        <jb-form
            slot="title"
            ref="titleForm"
            style="width: 100%;"
            :model="formData">
            <jb-form-item
                :rules="rules.name"
                property="name"
                error-display-type="tooltips"
                style="margin-bottom: 0;">
                <jb-input
                    v-model="formData.name"
                    class="name-input"
                    behavior="simplicity"
                    :placeholder="'...'"
                    :native-attributes="{
                        spellcheck: false,
                        autofocus: true,
                    }"
                    :maxlength="60" />
            </jb-form-item>
        </jb-form>
        <jb-form
            ref="editPlanForm"
            :model="formData"
            :rules="rules"
            form-type="vertical"
            v-test="{ type: 'form', value: 'editPlan' }">
            <jb-form-item style="margin-bottom: 40px;">
                <div class="section-title">
                    <span>{{ '' }}</span>
                    <span> {{ selectedVariable.length }} / {{ globalVariableList.length }} </span>
                </div>
                <render-global-var
                    :key="id"
                    :list="globalVariableList"
                    :select-value="selectedVariable"
                    @on-change="handleVariableChange"
                    :default-field="''"
                    mode="editOfPlan" />
            </jb-form-item>
            <jb-form-item
                :rules="rules.enableSteps"
                property="enableSteps">
                <div class="task-step-selection">
                    <div class="section-title">
                        <span>{{ '' }}</span>
                        <span> {{ formData.enableSteps.length }} / {{ taskStepList.length }} </span>
                    </div>
                    <div class="step-check">
                        <bk-button
                            v-if="hasSelectAll"
                            text
                            @click="handleDeselectAll">
                            {{ '' }}
                        </bk-button>
                        <bk-button
                            v-else
                            text
                            @click="handleSelectAll">
                            {{ '' }}
                        </bk-button>
                    </div>
                </div>
                <render-task-step
                    :key="id"
                    :list="taskStepList"
                    :select-value="formData.enableSteps"
                    :variable="globalVariableList"
                    mode="select"
                    @on-select="handleSelectStep" />
            </jb-form-item>
        </jb-form>
        <template #footer>
            <span v-bk-tooltips="isSubmitDisable ? '' : ''">
                <bk-button
                    theme="primary"
                    class="w120 mr10"
                    
                    :loading="submitLoading"
                    @click="handleSumbit"
                    v-test="{ type: 'button', value: 'editPlanSave' }">
                    {{ '' }}
                </bk-button>
            </span>
            <bk-button
                @click="handleCancle"
                v-test="{ type: 'button', value: 'editPlanCancel' }">
                {{ '' }}
            </bk-button>
        </template>
    </layout>
</template>
<script>
       import TaskPlanService from '@service/task-plan';
    import {
        findUsedVariable,
        leaveConfirm,
    } from '@utils/assist';
    import { planNameRule } from '@utils/validator';
    import RenderGlobalVar from '../../common/render-global-var';
    import RenderTaskStep from '../../common/render-task-step';
    import Layout from './components/layout';

    const getDefaultData = () => ({
        id: 0,
        name: '',
        enableSteps: [],
        templateId: 0,
        variables: [],
    });

    export default {
        name: '',
        components: {
            Layout,
            RenderGlobalVar,
            RenderTaskStep,
        },
        props: {
            id: {
                type: [
                    Number,
                    String,
                ],
                required: true,
            },
            templateId: {
                type: [
                    Number,
                    String,
                ],
                required: true,
            },
        },
        data () {
            return {
                name: '',
                formData: getDefaultData(),
                globalVariableList: [{},{},{}],
                taskStepList: [{},{},{}],
                isLoading: true,
                submitLoading: false,
            };
        },
        computed: {
            /**
             * @desc 
             * @return {Array}
             */
            selectedVariable () {
                const selectedSteps = this.taskStepList.filter(step => this.formData.enableSteps.includes(step.id));
                return findUsedVariable(selectedSteps);
            },
            /**
             * @desc 
             * @return {Boolean}
             */
            hasSelectAll () {
                return this.formData.enableSteps.length >= this.taskStepList.length;
            },
            /**
             * @desc 
             * @returns { Boolean }
             */
            isSubmitDisable () {
                return this.formData.enableSteps.length < 1;
            },
        },
        watch: {
            id: {
                handler (value) {
                    this.formData.id = value;
                    this.fetchData();
                },
                immediate: true,
            },
        },
        created () {
            this.rules = {
                name: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur',
                    },
                    {
                        validator: planNameRule.validator,
                        message: planNameRule.message,
                        trigger: 'blur',
                    },
                    {
                        validator: name => TaskPlanService.planCheckName({
                            templateId: this.templateId,
                            planId: this.formData.id,
                            name,
                        }),
                        message: '',
                        trigger: 'blur',
                    },
                ],
                enableSteps: [
                    {
                        validator: () => this.formData.enableSteps.length,
                        message: '',
                        trigger: 'blur',
                    },
                ],
            };
            
            this.formData.templateId = this.templateId;
        },
        methods: {
            /**
             * @desc 
             */
            fetchData () {
                this.isLoading = true;
                TaskPlanService.fetchPlanEditInfo({
                    id: this.formData.id,
                    templateId: this.templateId,
                }).then((data) => {
                    const { variableList, stepList, name } = data;
                    this.globalVariableList = variableList;
                    this.taskStepList = stepList;
                    
                    this.name = name;
                    this.formData.enableSteps = data.enableStepId;
                    this.formData.variables = variableList;
                    this.formData.name = name;
                })
                    .catch((error) => {
                        if ([
                            1,
                            400,
                        ].includes(error.code)) {
                            setTimeout(() => {
                                this.$router.push({
                                    name: 'taskList',
                                });
                            }, 3000);
                        }
                    })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            /**
             * @desc 
             */
            refresh () {
                this.fetchData();
            },
            /**
             * @desc 
             * @param {String} name
             */
            checkName (name) {
                return TaskPlanService.planCheckName({
                    templateId: this.templateId,
                    planId: this.formData.id,
                    name,
                });
            },
            /**
             * @desc 
             * @param {Array} variables
             */
            handleVariableChange (variables) {
                window.changeAlert = true;
                this.formData.variables = variables;
            },
            /**
             * @desc 
             */
            handleSelectAll () {
                this.formData.enableSteps = this.taskStepList.map(item => item.id);
            },
            /**
             * @desc 
             */
            handleDeselectAll () {
                this.formData.enableSteps = [];
            },
            /**
             * @desc 
             * @param {Object} stepData 
             */
            handleSelectStep (stepData) {
                const index = this.formData.enableSteps.findIndex(item => item === stepData.id);

                if (index > -1) {
                    this.formData.enableSteps.splice(index, 1);
                    return;
                }
                
                this.formData.enableSteps.push(stepData.id);

                if (this.formData.enableSteps.length) {
                    this.$refs.editPlanForm.clearError('enableSteps');
                }
            },
            /**
             * @desc 
             */
            handleSumbit () {
                this.submitLoading = true;
                Promise.all([
                    this.$refs.titleForm.validate(),
                    this.$refs.editPlanForm.validate(),
                ]).then(() => TaskPlanService.planUpdate(this.formData)
                    .then(() => {
                        window.changeAlert = false;
                        this.$emit('on-edit-success');
                        this.$bkMessage({
                            theme: 'success',
                            message: '',
                        });
                        this.handleCancle();
                    }))
                    .finally(() => {
                        this.submitLoading = false;
                    });
            },
            /**
             * @desc 
             */
            handleCancle () {
                leaveConfirm()
                    .then(() => {
                        this.$emit('on-edit-cancle');
                    });
            },
        },
    };
</script>
<style lang="postcss">
    @import "@/css/mixins/media";

    .task-plan-edit-box {
        .variable-batch-action {
            margin: 4px 0;
        }

        .layout-title {
            padding-bottom: 0 !important;
            border-bottom-color: transparent !important;

            .name-input {
                .bk-form-input {
                    font-size: 18px;
                    color: #313238;
                }

                .only-bottom-border {
                    padding-top: 9px;
                    padding-bottom: 16px;
                    padding-left: 0;
                }
            }
        }

        .section-title {
            font-size: 14px;
            line-height: 19px;
            color: #313238;
        }

        .task-step-selection {
            display: flex;
            width: 500px;
            height: 19px;
            margin-bottom: 16px;

            @media (--small-viewports) {
                width: 500px;
            }

            @media (--medium-viewports) {
                width: 560px;
            }

            @media (--large-viewports) {
                width: 620px;
            }

            @media (--huge-viewports) {
                width: 680px;
            }

            .step-check {
                margin-left: auto;
            }
        }
    }

</style>
