<template>
    <div
        class="jb-edit-input"
        :class="mode"
        :key="value">
        <template v-if="!isEditing">
            <div
                class="render-value-box"
                @click.stop="handleBlockShowEdit">
                <div class="value-text" v-bk-overflow-tips>
                    <slot v-bind:value="newVal">
                        <span>{{ newVal || '--' }}</span>
                    </slot>
                </div>
                <div class="edit-action-box">
                    <Icon
                        v-if="!isBlock && !isSubmiting"
                        type="edit-2"
                        class="edit-action"
                        @click.self.stop="handleShowEdit" />
                    <Icon
                        v-if="isSubmiting"
                        type="loading-circle"
                        class="edit-loading" />
                </div>
            </div>
        </template>
        <template v-else>
            <div class="edit-value-box" :class="{ 'edit-error': !!error }" @click.stop="">
                <bk-input
                    ref="input"
                    :value="newVal"
                    @change="handleInputChange"
                    @blur="handleInputBlur"
                    @keyup="handleInputEnter" />
                <div v-if="error" class="input-edit-info" v-bk-tooltips.top="error">
                    <Icon type="info" />
                </div>
            </div>
        </template>
    </div>
</template>
<script>
   
    export default {
        name: 'JbEditInput',
        props: {
            /**
             * @value block 
             * @value  
             */
            mode: {
                type: String,
                default: '',
            },
            /**
             * @desc 
             */
            field: {
                type: String,
                required: true,
            },
            /**
             * @desc 
             */
            value: {
                type: String,
                default: '',
            },
            /**
             * @desc 
             */
            width: {
                type: String,
                default: 'auto',
            },
            remoteHander: {
                type: Function,
                default: () => Promise.resolve(),
            },
            /**
             * @desc 
             */
            rules: {
                type: Array,
                default: () => [],
            },
        },
        data () {
            return {
                newVal: this.value,
                error: '',
                isEditing: false,
                isSubmiting: false,
            };
        },
        computed: {
            styles () {
                return {
                    width: this.width,
                };
            },
            isBlock () {
                return this.mode === 'block';
            },
        },
        watch: {
            value (newVal) {
                this.newVal = newVal;
            },
        },
        mounted () {
            this.isValidatoring = false;
            document.body.addEventListener('click', this.handleHideEdit);
            this.$once('hook:beforeDestroy', () => {
                document.body.removeEventListener('click', this.handleHideEdit);
            });
        },
        methods: {
            /**
             * @desc 
             */
            doValidator () {
                const checkValidator = (rule, value) => new Promise((resolve, reject) => {
                    if (rule.required && !value) {
                        reject(rule.message);
                    }
                    // 
                    if (rule.validator && (typeof rule.validator === 'function')) {
                        const result = rule.validator(value);
                        if (result.then) {
                            result.then((data) => {
                                if (data) {
                                    return resolve();
                                }
                                return reject(rule.message);
                            }).catch(() => {
                                reject(rule.message);
                            });
                        } else if (result) {
                            return resolve();
                        } else {
                            return reject(rule.message);
                        }
                    } else {
                        resolve();
                    }
                });
                
                const allPromise = this.rules.map(rule => checkValidator(rule, this.newVal));
                this.isValidatoring = true;
                return Promise.all(allPromise).finally(() => {
                    this.isValidatoring = false;
                });
            },
            /**
             * @desc 
             */
            triggerChange () {
                this.doValidator()
                    .then(() => {
                        this.isEditing = false;
                        if (this.newVal === this.value) {
                            return;
                        }
                        this.isSubmiting = true;
                        this.remoteHander({
                            [this.field]: this.newVal,
                        }).then(() => {
                            this.$emit('on-change', {
                                [this.field]: this.newVal,
                            });
                            this.messageSuccess('');
                        })
                            .catch(() => {
                                this.newVal = this.value;
                            })
                            .finally(() => {
                                this.isSubmiting = false;
                            });
                    })
                    .catch((error) => {
                        this.error = error;
                    });
            },
            handleBlockShowEdit () {
                if (!this.isBlock) {
                    return;
                }
                this.handleShowEdit();
            },
            /**
             * @desc input
             */
            handleShowEdit () {
                document.body.click();
                this.isEditing = true;
                this.$nextTick(() => {
                    this.$refs.input.focus();
                });
            },
            /**
             * @desc input 
             * @param {String} value 
             */
            handleInputChange (value) {
                this.newVal = value.trim();
            },
            /**
             * @desc input 
             */
            handleInputBlur () {
                this.triggerChange();
            },
            /**
             * @desc input enter 
             * @param {String} value 
             * @param {Object} event dom 
             */
            handleInputEnter (value, event) {
                if (!this.isEditing) return;
                if (event.key === 'Enter' && event.keyCode === 13) {
                    this.triggerChange();
                }
            },
            /**
             * @desc  input 
             * @param {Object} event dom 
             */
            handleHideEdit (event) {
                if (this.isValidatoring || this.error) {
                    return;
                }
                if (event.path && event.path.length > 0) {
                    // eslint-disable-next-line no-plusplus
                    for (let i = 0; i < event.path.length; i++) {
                        const target = event.path[i];
                        if (target.className === 'jb-edit-input') {
                            return;
                        }
                    }
                }
                this.isEditing = false;
            },
        },
    };
</script>
<style lang='postcss'>
    .jb-edit-input {
        &.block {
            position: relative;
            margin-left: -10px;
            cursor: pointer;

            .render-value-box {
                padding-left: 10px;

                &:hover {
                    background: #f0f1f5;
                }
            }

            .edit-action-box {
                position: absolute;
                top: 0;
                right: 10px;
                width: 16px;
            }
        }

        .render-value-box {
            position: relative;
            display: flex;
            height: 30px;
            min-width: 36px;
            min-height: 28px;

            &:hover {
                .edit-action {
                    opacity: 100%;
                    transform: scale(1);
                }
            }
        }

        .value-text {
            overflow: hidden;
            line-height: 30px;
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        .edit-action-box {
            display: flex;
            align-items: center;
            min-height: 1em;
            margin-right: auto;
            font-size: 16px;
            color: #979ba5;

            .edit-action {
                padding: 6px 0 6px 2px;
                cursor: pointer;
                opacity: 0%;
                transform: scale(0);
                transition: 0.15s;
                transform-origin: left center;

                &:hover {
                    color: #3a84ff;
                }
            }

            .edit-loading {
                position: absolute;
                top: 9px;
                margin-left: 2px;
                animation: rotate-loading 1s linear infinite;
            }
        }

        .edit-value-box {
            position: relative;
            width: 100%;
            font-size: 0;

            &.edit-error {
                .bk-form-input {
                    border-color: #ea3636 !important;
                }
            }

            .input-edit-info {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
                display: flex;
                align-items: center;
                padding: 0 10px;
                font-size: 16px;
                color: #ea3636;
            }

            .bk-form-input {
                height: 30px;
            }
        }
    }
</style>
