<template>
    <div class="step-distro-file">
        <jb-form ref="form" :model="formData" fixed :label-width="formMarginLeftWidth">
            <card-layout :title="''" class="block">
                <item-factory
                    name="stepName"
                    field="name"
                    :placeholder="'...'"
                    :form-data="formData"
                    @on-change="handleChange" />
                <item-factory
                    name="timeout"
                    field="timeout"
                    :form-data="formData"
                    @on-change="handleChange" />
                <item-factory
                    name="errorHandle"
                    field="ignoreError"
                    :form-data="formData"
                    @on-change="handleChange" />
                <item-factory
                    name="speedLimit"
                    field="uploadSpeedLimit"
                    :label="''"
                    :form-data="formData"
                    @on-change="handleChange" />
                <item-factory
                    name="speedLimit"
                    field="downloadSpeedLimit"
                    :label="''"
                    :form-data="formData"
                    @on-change="handleChange" />
            </card-layout>
            <card-layout :title="''" class="block">
                <item-factory
                    name="sourceFileOfTemplate"
                    field="fileSourceList"
                    :variable="variable"
                    :form-data="formData"
                    @on-change="handleChange" />
            </card-layout>
            <card-layout :title="''" class="block">
                <item-factory
                    ref="targetPath"
                    name="targetPath"
                    field="path"
                    tips-placement="top"
                    :form-data="formData"
                    @on-change="handleChange" />
                <item-factory
                    name="transferMode"
                    field="transferMode"
                    :form-data="formData"
                    @on-change="handleChange" />
                <item-factory
                    name="executeAccount"
                    field="account"
                    :form-data="formData"
                    @on-change="handleChange" />
                <item-factory
                    name="targetServerOfTemplate"
                    field="server"
                    :variable="variable"
                    :form-data="formData"
                    @on-change="handleChange" />
            </card-layout>
        </jb-form>
    </div>
</template>
<script>
    import _ from 'lodash';
       import { mapState } from 'vuex';
    import TaskStepModel from '@model/task/task-step';
    import TaskHostNodeModel from '@model/task-host-node';
    import CardLayout from '@components/task-step/file/card-layout';
    import ItemFactory from '@components/task-step/file/item-factory';
    import {
        genDefaultName,
        compareHost,
        detectionSourceFileDupLocation,
    } from '@utils/assist';

    const getDefaultData = () => ({
        id: 0,
        // 
        name: genDefaultName(''),
        // 
        fileSourceList: [{},{},{}],
        // 
        timeout: 7200,
        // 
        uploadSpeedLimit: 0,
        //  1 -  2 - 3 - 
        transferMode: 2,
        //  0 -  1 - 
        ignoreError: 0,
        // 
        downloadSpeedLimit: 0,

        // accountpathserver
        //  fileDestination
        // fileDestination: {
        //     account: '', // 
        //     path: '', // 
        //     server: {} // 
        // }
        account: '',
        path: '',
        server: new TaskHostNodeModel({}),
    });

    export default {
        name: '',
        components: {
            CardLayout,
            ItemFactory,
        },
        inheritAttrs: false,
        props: {
            variable: {
                type: Array,
                default: () => [],
            },
            data: {
                type: Object,
                default: () => [],
            },
        },
        data () {
            return {
                formData: getDefaultData(),
            };
        },
        computed: {
            ...mapState('distroFile', {
                isEditNewSourceFile: state => state.isEditNewSourceFile,
                isLocalFileUploading: state => state.isLocalFileUploading,
                isLocalFileUploadFailed: state => state.isLocalFileUploadFailed,
            }),
            formMarginLeftWidth () {
                return 'zh-CN' === 'en-US' ? 140 : 110;
            },
        },
        watch: {
            data: {
                handler (newData) {
                    if (_.isEmpty(newData)) {
                        return;
                    }
                    const {
                        account,
                        path,
                        server,
                    } = newData.fileDestination;

                    const originData = { ...newData };
                    delete originData.fileDestination;

                    this.formData = {
                        ...this.formData,
                        ...originData,
                        account,
                        path,
                        server,
                    };
                    setTimeout(() => {
                        this.$refs.form.validate();
                    });
                },
                immediate: true,
            },
        },
        mounted () {
            window.IPInputScope = 'FILE_DISTRIBUTION';
            this.$once('hook:beforeDestroy', () => {
                window.IPInputScope = '';
            });
        },
        methods: {
            /**
             * @desc 
             * @param {String} field 
             * @param {Any} value 
             */
            handleChange (field, value) {
                this.formData[field] = value;
            },
            /**
             * @desc 
             *
             * 1
             * 2
             *   - 
             */
            submit () {
                return Promise.resolve()
                    // 
                    .then(() => new Promise((resolve, reject) => {
                        if (!this.isEditNewSourceFile) {
                            return resolve();
                        }
                        this.$bkInfo({
                            title: '',
                            type: 'warning',
                            okText: '',
                            cancelText: '',
                            confirmFn: () => {
                                resolve();
                            },
                            cancelFn: () => {
                                reject(new Error('save'));
                            },
                        });
                    }))
                    // 
                    .then(() => new Promise((resolve, reject) => {
                        let sameHost = false;
                        // eslint-disable-next-line no-plusplus
                        for (let i = 0; i < this.formData.fileSourceList.length; i++) {
                            const currentFileSource = this.formData.fileSourceList[i];
                            // 
                            if (currentFileSource.fileType === TaskStepModel.fileStep.TYPE_SERVER) {
                                if (compareHost(this.formData.server, currentFileSource.host)) {
                                    sameHost = true;
                                    break;
                                }
                            }
                        }
                        if (sameHost) {
                            this.$bkInfo({
                                title: '',
                                subTitle: ' cp ',
                                width: 500,
                                okText: '',
                                cancelText: '',
                                confirmFn: () => {
                                    reject(new Error('save'));
                                },
                                cancelFn: () => {
                                    resolve();
                                },
                            });
                        } else {
                            resolve();
                        }
                    }))
                    // 
                    .then(() => new Promise((resolve, reject) => {
                        if (detectionSourceFileDupLocation(this.formData.fileSourceList)) {
                            // 
                            this.$bkInfo({
                                title: '',
                                subTitle: ' [IP] ',
                                okText: '',
                                cancelText: '',
                                closeIcon: false,
                                width: 500,
                                confirmFn: () => {
                                    // 
                                    this.$refs.targetPath.$el.scrollIntoView();
                                    this.$refs.targetPath.$el.querySelector('.bk-form-input').focus();
                                    reject(new Error('transferMode change'));
                                },
                                cancelFn: () => {
                                    resolve();
                                },
                            });
                        } else {
                            resolve();
                        }
                    }))
                    // 
                    .then(() => {
                        const {
                            id,
                            name,
                            timeout,
                            uploadSpeedLimit,
                            downloadSpeedLimit,
                            transferMode,
                            ignoreError,
                            fileSourceList,
                            account,
                            path,
                            server,
                        } = this.formData;

                        const result = {
                            id,
                            name,
                            delete: 0,
                            type: 2,
                            fileStepInfo: {
                                timeout,
                                uploadSpeedLimit,
                                downloadSpeedLimit,
                                transferMode,
                                ignoreError,
                                fileSourceList,
                                fileDestination: {
                                    account,
                                    path,
                                    server,
                                },
                                
                            },
                        };
                        return this.$refs.form.validate()
                            // 
                            .then(() => {
                                this.$emit('on-change', result, true);
                            })
                            // 
                            .catch(() => new Promise((resolve, reject) => {
                                let confirmInfo = null;
                                const handleClose = () => {
                                    confirmInfo.close();
                                    reject(new Error('not save'));
                                };
                                const subHeader = () => (
                                <div>
                                    <div style="text-align: center">
                                        <bk-button
                                            onClick={handleClose}
                                            style="width: 96px"
                                            theme="primary">
                                            { '' }
                                        </bk-button>
                                    </div>
                                </div>
                                );
                                if (this.isLocalFileUploading) {
                                    confirmInfo = this.$bkInfo({
                                        type: 'error',
                                        title: '',
                                        subHeader: subHeader(),
                                        showFooter: false,
                                    });
                                } else if (this.isLocalFileUploadFailed) {
                                    confirmInfo = this.$bkInfo({
                                        type: 'error',
                                        title: '',
                                        subHeader: subHeader(),
                                        showFooter: false,
                                    });
                                } else {
                                    this.$emit('on-change', result, false);
                                    resolve();
                                }
                            }));
                    });
            },
        },
    };
</script>
<style lang='postcss'>
    .step-distro-file {
        .card-box {
            padding-left: 0;
            margin-bottom: 6px;

            .card-title {
                padding-left: 30px;
            }
        }
    }
</style>
