<template>
    <div class="variable-type-host" :class="{ 'variable-value-error': isError }">
        <div>
            <div>
                <bk-button
                    class="mr10"
                    
                    @click="handleChooseIp"
                    v-bk-tooltips="descPopover">
                    <Icon type="plus" />
                    {{ '' }}
                </bk-button>
                <bk-button
                    v-show="isNotEmpty"
                    
                    @click="handleClear">
                    {{ '' }}
                </bk-button>
            </div>
            <server-panel
                v-show="isNotEmpty"
                ref="choostIP"
                class="host-value-panel"
                :host-node-info="hostNodeInfo"
                detail-fullscreen
                :editable="!readonly"
                @on-change="handleChange" />
            <p v-if="isError" class="variable-error">{{ '' }}</p>
        </div>
        <choose-ip
            v-model="isShowChooseIp"
            :host-node-info="hostNodeInfo"
            @on-change="handleChange" />
    </div>
</template>
<script>
    import TaskHostNodeModel from '@model/task-host-node';
    import ChooseIp from '@components/choose-ip';
    import ServerPanel from '@components/choose-ip/server-panel';

    export default {
        components: {
            ChooseIp,
            ServerPanel,
        },
        props: {
            data: {
                type: Object,
                required: true,
            },
            readonly: {
                type: Boolean,
                default: false,
            },
        },
        data () {
            return {
                isShowChooseIp: false,
                hostNodeInfo: {},
            };
        },
        computed: {
            isNotEmpty () {
                return !TaskHostNodeModel.isHostNodeInfoEmpty(this.hostNodeInfo);
            },
            isError () {
                if (this.data.required !== 1) {
                    return false;
                }
                return !this.isNotEmpty;
            },
            descPopover () {
                return {
                    theme: 'light',
                    extCls: 'variable-desc-tippy',
                    trigger: 'click mouseenter',
                    hideOnClick: false,
                    content: this.data.description,
                    disabled: !this.data.description,
                };
            },
        },
        created () {
            this.init();
        },
        methods: {
            /**
             * @desc 
             */
            init () {
                if (this.data.defaultTargetValue) {
                    this.hostNodeInfo = this.data.defaultTargetValue.hostNodeInfo;
                } else {
                    this.hostNodeInfo = this.data.targetValue.hostNodeInfo;
                }
            },
            /**
             * @desc 
             */
            removeAllInvalidHost () {
                window.changeAlert = true;
                this.$refs.choostIP.removeAllInvalidHost();
            },
            /**
             * @desc 
             */
            handleChooseIp () {
                this.isShowChooseIp = true;
            },
            /**
             * @desc 
             */
            handleClear () {
                const { hostNodeInfo } = new TaskHostNodeModel({});
                this.hostNodeInfo = hostNodeInfo;
                window.changeAlert = true;
            },
            /**
             * @desc 
             */
            handleChange (hostNodeInfo) {
                this.hostNodeInfo = Object.freeze(hostNodeInfo);
                window.changeAlert = true;
            },
            /**
             * @desc 
             */
            reset () {
                this.init();
            },
            /**
             * @desc 
             * @returns {Promise}
             */
            validate () {
                const { type, id, name } = this.data;
                
                const data = {
                    id,
                    name,
                    type,
                    value: '',
                    targetValue: {
                        hostNodeInfo: this.hostNodeInfo,
                    },
                };
                return new Promise((resolve, reject) => {
                    if (this.isError) {
                        return reject(new Error('host error'));
                    }
                    resolve(data);
                });
            },
        },
    };
</script>
<style lang='postcss' scoped>
    .variable-type-host {
        .host-value-panel {
            margin-top: 10px;
        }
    }

</style>
