<template>
    <jb-dialog
        :value="value"
        class="choose-source-file"
        header-position="left"
        :esc-close="false"
        :mask-close="false"
        :width="1200"
        :title="''"
        @input="handleCancle">
        <div v-bkloading="{ isLoading }">
            <component
                v-if="value"
                :is="panelCom"
                class="dialog-content"
                :file-source-id="fileSourceId"
                :file-location="fileLocation"
                @on-source-change="handleSourceChange"
                @on-file-change="handelFileChange" />
        </div>
        <template #footer>
            <bk-button
                class="mr10"
                theme="primary"
                
                @click="handleSubmit">
                <span>{{ '' }}</span>
                <span v-if="!isSelectedEmpty" class="result-nums">{{ fileLocation.length }}</span>
            </bk-button>
            <bk-button @click="handleCancle">{{ '' }}</bk-button>
        </template>
    </jb-dialog>
</template>
<script>
    import SourceFileVO from '@domain/variable-object/source-file';
    import SelectFileSource from './select-file-source';
    import SelectFile from './select-file';

    const SELECT_FILE_SOURCE = 'selectFileSource';
    const SELECT_FILE = 'selectFile';
    
    export default {
        components: {
            SelectFileSource,
            SelectFile,
        },
        props: {
            value: {
                type: Boolean,
                defaule: false,
            },
            sourceFile: {
                type: Object,
                defaule: () => ({}),
            },
        },
        data () {
            return {
                isLoading: false,
                selectStep: SELECT_FILE_SOURCE,
                fileSourceId: '',
                fileLocation: [],
            };
        },
        computed: {
            panelCom () {
                if (!this.fileSourceId) {
                    return SelectFileSource;
                }
                const comMap = {
                    [SELECT_FILE_SOURCE]: SelectFileSource,
                    [SELECT_FILE]: SelectFile,
                };
                return comMap[this.selectStep];
            },
            isSelectedEmpty () {
                return this.fileLocation.length < 1;
            },
        },
        watch: {
            value: {
                /**
                 * @desc 
                 *
                 * 1fileSourceId
                 * fileSourceIdbucketName
                 * 2fileSourceId
                 *  
                 *
                 */
                handler (value) {
                    if (!value) {
                        return;
                    }
                    if (this.sourceFile.fileSourceId) {
                        const {
                            fileSourceId,
                            fileLocation,
                        } = this.sourceFile;
                        this.fileSourceId = fileSourceId;
                        this.fileLocation = fileLocation;
                        this.selectStep = SELECT_FILE;
                    } else {
                        this.fileSourceId = '';
                        this.fileLocation = [];
                        this.selectStep = SELECT_FILE_SOURCE;
                    }
                },
                immediate: true,
            },
        },
        methods: {
            /**
             * @desc 
             * @param {Object} fileSource 
             *
             * fileSourcebucket
             *
             */
            handleSourceChange (fileSource) {
                this.fileSourceId = fileSource.id;
                this.selectStep = SELECT_FILE;
                this.fileLocation = [];
            },
            /**
             * @desc 
             * @param {Array} fileLocation 
             */
            handelFileChange (fileLocation) {
                this.fileLocation = Object.freeze([...fileLocation]);
            },

            /**
             * @desc 
             *
             * ,
             * ,
             */
            handleSubmit () {
                const fileSourceObj = new SourceFileVO({
                    fileSourceId: this.fileSourceId,
                    fileType: 3,
                    fileLocation: this.fileLocation,
                });
                this.$emit('on-change', fileSourceObj);
                this.handleCancle();
            },

            /**
             * @desc 
             */
            handleCancle () {
                this.$emit('input', false);
            },

        },
    };
</script>
<style lang="postcss">
    .choose-source-file {
        .result-nums {
            display: inline-block;
            min-width: 18px;
            min-height: 18px;
            padding: 0 5px;
            font-size: 12px;
            line-height: 18px;
            color: #3a84ff;
            text-align: center;
            background-color: #fff;
            border-radius: 10px;
        }

        .dialog-content {
            height: 570px;
        }
    }
</style>
