<template>
    <jb-form :model="formData" ref="varArrayForm">
        <jb-form-item :label="''">
            <div class="array-type-group">
                <bk-radio-group :value="arrayType">
                    <bk-radio-button
                        class="item"
                        :value="5"
                        >
                        {{ '' }}
                    </bk-radio-button>
                    <bk-radio-button
                        class="item"
                        :value="6"
                        >
                        {{ '' }}
                    </bk-radio-button>
                </bk-radio-group>
            </div>
        </jb-form-item>
        <jb-form-item :label="''">
            <bk-input v-model="formData.name" disabled />
        </jb-form-item>
        <jb-form-item :label="''">
            <bk-input
                v-model="formData.defaultValue"
                :native-attributes="{ autofocus: 'autofocus' }" />
        </jb-form-item>
        <jb-form-item :label="''">
            <bk-input v-model="formData.description" disabled type="textarea" :row="5" maxlength="100" />
        </jb-form-item>
        <jb-form-item>
            <bk-checkbox
                v-model="formData.changeable"
                disabled
                :true-value="1"
                :false-value="0">
                {{ '' }}
            </bk-checkbox>
        </jb-form-item>
        <jb-form-item>
            <bk-checkbox
                v-model="formData.required"
                disabled
                :true-value="1"
                :false-value="0">
                {{ '' }}
            </bk-checkbox>
        </jb-form-item>
    </jb-form>
</template>
<script>
    const getDefaultData = () => ({
        id: 0,
        delete: 0,
        // 
        name: '',
        // 
        defaultValue: '',
        // 
        description: '',
        //  0- 1-
        changeable: 0,
        //  0- 1-
        required: 0,
    });
    export default {
        name: 'VarString',
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                },
            },
        },
        data () {
            return {
                arrayType: 5,
                formData: getDefaultData(),
            };
        },
        watch: {
            data: {
                handler (value) {
                    if (Object.keys(value).length) {
                        const { name, defaultValue, description, changeable, required, id, type } = value;
                        this.formData = {
                            name,
                            defaultValue,
                            description,
                            changeable,
                            required,
                            id,
                            delete: value.delete,
                        };
                        this.arrayType = type;
                    }
                },
                immediate: true,
            },
        },
        methods: {
            submit () {
                return Promise.resolve({
                    ...this.formData,
                    type: this.arrayType,
                });
            },

            reset () {
                this.formData = getDefaultData();
            },
        },
    };
</script>
<style lang='postcss'>
    .array-type-group {
        position: relative;
        z-index: 1;

        .item {
            .bk-radio-button-text {
                width: 108px;
            }
        }
    }
</style>
