<template>
    <div
        v-if="isShow"
        v-bk-tooltips="''"
        class="job-back-top"
        :class="classes"
        :style="styles"
        @click="handleBackTop">
        <Icon type="up-to-top" />
    </div>
</template>
<script>
    import _ from 'lodash';
    import {
        scrollTopSmooth,
    } from '@utils/assist';

    export default {
        name: '',
        props: {
            // 
            target: {
                type: Function,
                default: () => document.querySelector('.jb-navigation-main').querySelector('.scroll-faker-content'),
            },
            size: {
                type: String,
                default: 'normal',
            },
            fixed: {
                type: Boolean,
                default: true,
            },
        },
        data () {
            return {
                isShow: false,
            };
        },
        computed: {
            classes () {
                return `theme-${this.size}`;
            },
            styles () {
                if (!this.fixed) {
                    return {};
                }
                return {
                    position: 'fixed',
                    right: '34px',
                    bottom: '92px',
                };
            },
        },
        mounted () {
            this.smartPosition();
            window.addEventListener('resize', this.smartPosition);
            const observer = new MutationObserver((payload) => {
                this.smartPosition();
            });
            observer.observe(this.target(), {
                subtree: true,
                childList: true,
                attributeName: true,
                characterData: true,
            });
            this.$once('hook:beforeDestroy', () => {
                observer.takeRecords();
                observer.disconnect();
                window.removeEventListener('resize', this.smartPosition);
            });
        },
        methods: {
            /**
             * @desc 
             */
            smartPosition: _.debounce(function () {
                const $srollContainer = this.target();
                if (!$srollContainer) {
                    return;
                }
                const selfHeight = $srollContainer.getBoundingClientRect().height;
                const { scrollHeight } = $srollContainer;
                this.isShow = scrollHeight > selfHeight + 30;
            }, 300),
            /**
             * @desc 
             */
            handleBackTop () {
                const $srollContainer = this.target();
                scrollTopSmooth($srollContainer, 0);
            },
        },
    };
</script>
<style lang='postcss'>
    .job-back-top {
        display: flex;
        width: 28px;
        height: 28px;
        color: #fff;
        cursor: pointer;
        background: rgb(0 0 0 / 25%);
        border-radius: 50%;
        align-items: center;
        justify-content: center;

        &:hover {
            background: rgb(0 0 0 / 40%);
        }

        &.theme-normal {
            width: 36px;
            height: 36px;
            font-size: 20px;
        }

        &.theme-small {
            width: 28px;
            height: 28px;
            font-size: 16px;
        }
    }
</style>
