<template>
    <div>
        <jb-form-item
            :label="''"
            required
            :property="field"
            :rules="rules">
            <div
                class="form-item-content"
                @mouseenter="handleMouseenter"
                @mouseleave="handleMouseleave">
                <bk-input
                    ref="input"
                    :placeholder="''"
                    @focus="handleInputFocus"
                    @blur="handleInputBlur"
                    @change="handleChange"
                    :value="formData[field]" />
            </div>
        </jb-form-item>
        <div style="display: none;">
            <div
                ref="targetPathTips"
                class="target-path-tips"
                @mouseleave="handleMouseleave"
                @mouseenter="handleTipsMouseenter">
                <div class="row">{{ 'linux\//data/xx' }}</div>
                <div class="row">{{ 'WindowsD:\\tmp\\' }}</div>
                <div class="row">
                    <div>{{ '' }}</div>
                    <table class="target-path-demo">
                        <thead>
                            <tr>
                                <th>{{ '' }}</th>
                                <th>{{ '' }}</th>
                                <th>{{ '' }}</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td @click="handleCopy('[FILESRCIP]')">
                                    <div class="copy-box">
                                        <span class="copy-value">[FILESRCIP]</span>
                                        <span class="copy-tips">{{ '' }}</span>
                                    </div>
                                </td>
                                <td>{{ 'IP' }}</td>
                                <td>0_192.168.0.100</td>
                            </tr>
                            <tr>
                                <td @click="handleCopy('[DATE:yyyy-MM-dd]')">
                                    <div class="copy-box">
                                        <span class="copy-value">[DATE:yyyy-MM-dd]</span>
                                        <span class="copy-tips">{{ '' }}</span>
                                    </div>
                                </td>
                                <td>{{ '' }}</td>
                                <td>2021-01-01</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="row">{{ ' yyyy-MM-dd_HH:mm:ss' }}</div>
            </div>
        </div>
    </div>
</template>
<script>
    import _ from 'lodash';
    import Tippy from 'bk-magic-vue/lib/utils/tippy';
       import {
        execCopy,
    } from '@utils/assist';
    import {
        filePathRule,
    } from '@utils/validator';

    export default {
        props: {
            field: {
                type: String,
                required: true,
            },
            formData: {
                type: Object,
                default: () => ({}),
            },
            tipsPlacement: {
                type: String,
                default: 'right-start',
            },
        },
        watch: {
            tipsPlacement (tipsPlacement) {
                this.popperInstance && this.popperInstance.set({
                    placement: tipsPlacement,
                });
            },
        },
        created () {
            this.rules = [
                {
                    required: true,
                    message: '',
                    trigger: 'blur',
                },
                {
                    validator: filePathRule.validator,
                    message: '',
                    trigger: 'blur',
                },
            ];
            this.isMouseenter = false;
        },
        beforeDestroy () {
            if (this.popperInstance) {
                this.popperInstance.hide();
                this.popperInstance.destroy();
            }
        },
        methods: {
            /**
             * @desc tips
             */
            showTips () {
                if (!this.popperInstance) {
                    this.popperInstance = Tippy(this.$refs.input.$el, {
                        arrow: true,
                        placement: this.tipsPlacement,
                        trigger: 'manual',
                        theme: 'light',
                        interactive: true,
                        hideOnClick: false,
                        animation: 'slide-toggle',
                        lazy: false,
                        size: 'small',
                        boundary: 'window',
                        distance: 20,
                        zIndex: window.__bk_zIndex_manager.nextZIndex(), // eslint-disable-line no-underscore-dangle
                    });
                    this.popperInstance.setContent(this.$refs.targetPathTips);
                }
                
                this.popperInstance.show();
            },
            /**
             * @desc tips
             */
            hideTips () {
                if (this.isMouseenter) {
                    return;
                }
                this.popperInstance.hide();
            },
            /**
             * @desc tips
             */
            handleMouseenter () {
                clearTimeout(this.hideTimer);
                this.showTips();
            },
            /**
             * @desc tips
             */
            handleMouseleave () {
                this.isMouseenter = false;
                this.hideTimer = setTimeout(() => {
                    if (!this.isInputFocus) {
                        this.hideTips();
                    }
                }, 100);
            },
            /**
             * @desc ips
             */
            handleInputFocus () {
                this.isInputFocus = true;
                this.showTips();
            },
            /**
             * @desc ips
             */
            handleInputBlur () {
                this.isInputFocus = false;
                this.hideTips();
            },
            /**
             * @desc tipstips
             */
            handleTipsMouseenter () {
                clearTimeout(this.hideTimer);
                this.isMouseenter = true;
            },
            /**
             * @desc 
             * @param {String} variable 
             */
            handleCopy (variable) {
                execCopy(variable);
            },
            /**
             * @desc 
             */
            handleChange (value) {
                this.$emit('on-change', this.field, _.trim(value));
            },
        },
    };
</script>
<style lang='postcss' scoped>
    .target-path-tips {
        font-size: 12px;
        line-height: 16px;
        color: #63656e;

        .row {
            position: relative;
            padding-left: 12px;
            margin-bottom: 6px;

            &::before {
                position: absolute;
                top: 6px;
                left: 0;
                width: 4px;
                height: 4px;
                background: currentColor;
                border-radius: 50%;
                content: "";
            }
        }

        .target-path-demo {
            width: 100%;
            margin-top: 6px;

            th,
            td {
                height: 40px;
                padding: 0 10px;
                border-bottom: 1px solid #dfe0e5;
            }

            th {
                background-color: #fafbfd;
            }

            .copy-box {
                display: flex;
                width: 160px;
                height: 26px;
                padding: 0 10px;
                margin: 0 -10px;
                overflow: hidden;
                line-height: 26px;
                cursor: pointer;
                border-radius: 2px;

                &:hover {
                    background: #f0f1f5;

                    .copy-tips {
                        display: block;
                    }
                }

                .copy-value {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }

                .copy-tips {
                    display: none;
                    margin-left: auto;
                    flex: 0 0 auto;
                }
            }
        }
    }
</style>
