<template>
    <jb-form-item :label="''">
        <div class="error-handle-wraper">
            <bk-checkbox
                v-bk-tooltips="tips"
                :value="formData[field]"
                :true-value="1"
                :false-value="0"
                @change="handleChange">
                {{ '' }}
            </bk-checkbox>
        </div>
    </jb-form-item>
</template>
<script>
   
    export default {
        name: '',
        props: {
            field: {
                type: String,
                required: true,
            },
            formData: {
                type: Object,
                default: () => ({}),
            },
        },
        created () {
            this.tips = {
                content: '',
                width: 310,
                placement: 'right',
            };
        },
        methods: {
            handleChange (value) {
                this.$emit('on-change', this.field, value);
            },
        },
    };
</script>
<style lang="postcss">
    .error-handle-wraper {
        display: flex;
        align-items: center;
        height: 100%;

        .bk-checkbox-text {
            border-bottom: 1px dashed #c4c6cc;
        }
    }
</style>
