<template>
    <div class="choose-ip-server-ip-input">
        <div @click="handleInputClick">
            <bk-input
                ref="input"
                type="textarea"
                :class="{
                    'focus-error': isFocusError,
                }"
                :rows="inputRows"
                :value="ipInputText"
                :placeholder="' IP IP   ; , | \n 0:192.168.1.101 '"
                @change="handleIPChange" />
        </div>
        <div class="input-action">
            <div v-if="isError" class="input-error">
                <div>{{ '' }}</div>
                <div v-if="invalidIPList.length > 0">
                    <span>{{ 'IP ' }}</span>
                    <Icon
                        type="ip-audit"
                        class="error-action"
                        v-bk-tooltips="''"
                        @click="handleHighlightInvilad" />
                    <Icon
                        type="delete"
                        class="error-action"
                        v-bk-tooltips="''"
                        @click="handleRemoveInvalid" />
                </div>
                <div v-if="invalidIPList.length > 0 && errorIPList.length > 0"></div>
                <div v-if="errorIPList.length > 0">
                    <span>{{ '' }}</span>
                    <Icon
                        type="ip-audit"
                        class="error-action"
                        v-bk-tooltips="''"
                        @click="handleHightlightError" />
                    <Icon
                        type="delete"
                        class="error-action"
                        v-bk-tooltips="''"
                        @click="handleRemoveError" />
                </div>
            </div>
            <bk-button
                class="submit-btn"
                theme="primary"
                outline
                :loading="isSubmiting"
                @click="handleAddHost">
                <span>{{ '' }}</span>
                <div
                    v-if="inputItemList.length > 0"
                    ref="inputNumber"
                    class="server-input-number">
                    <span class="text">{{ inputItemList.length }}</span>
                </div>
            </bk-button>
        </div>
    </div>
</template>
<script>
    import _ from 'lodash';
    import AppManageService from '@service/app-manage';
    import { encodeRegexp } from '@utils/assist';
    import { generateHostRealId } from './utils';

    export default {
        name: '',
        inheritAttrs: false,
        props: {
            previewId: {
                type: String,
                required: true,
            },
            dialogHeight: {
                type: Number,
                required: true,
            },
        },
        data () {
            return {
                isSubmiting: false,
                ipInputText: '',
                inputItemList: [{},{},{}],
                invalidIPList: [{},{},{}],
                errorIPList: [{},{},{}],
                isFocusError: false,
            };
        },
        computed: {
            inputRows () {
                const offsetTop = 65;
                const offsetBottom = 130;
                const inputPadding = 15;

                return Math.floor((this.dialogHeight - offsetTop - offsetBottom - inputPadding) / 18);
            },
            isError () {
                return this.invalidIPList.length > 0 || this.errorIPList.length > 0;
            },
        },
        methods: {
            /**
             * @desc 
             */
            updateInputValue () {
                this.ipInputText = [
                    ...this.invalidIPList,
                    ...this.errorIPList,
                ].join('\n');
                // fix: bk-input
                setTimeout(() => {
                    this.$refs.input.setCurValue(this.ipInputText);
                });
            },
            /**
             * @desc 
             */
            handleInputClick () {
                this.isFocusError = false;
            },
            /**
             * @desc  IP 
             */
            handleHighlightInvilad () {
                this.isFocusError = true;
                const $inputEl = this.$refs.input.$el.querySelector('textarea');
                const errorText = this.invalidIPList.join('\n');
                $inputEl.focus();
                $inputEl.selectionStart = 0;
                $inputEl.selectionEnd = errorText.length;
            },
            /**
             * @desc  IP 
             */
            handleRemoveInvalid () {
                this.invalidIPList = [];
                this.updateInputValue();
            },
            /**
             * @desc  IP 
             */
            handleHightlightError () {
                this.isFocusError = true;
                const $inputEl = this.$refs.input.$el.querySelector('textarea');
                $inputEl.focus();
                const invalidText = this.invalidIPList.join('\n');
                const errorText = this.errorIPList.join('\n');
                const startIndex = invalidText.length > 0 ? invalidText.length + 1 : 0;
                $inputEl.selectionStart = startIndex;
                $inputEl.selectionEnd = startIndex + errorText.length;
            },
            /**
             * @desc  IP 
             */
            handleRemoveError () {
                this.errorIPList = [];
                this.updateInputValue();
            },
            /**
             * @desc 
             * @param {String} value 
             */
            handleIPChange: _.throttle(function (value) {
                const realValue = _.trim(value);
                this.invalidIPList = [];
                this.errorIPList = [];
                if (!realValue) {
                    this.inputItemList = [];
                    // 
                    this.$emit('on-input-change', false);
                } else {
                    this.inputItemList = value.split(/[;,\n|]+/).filter(_ => !!_);
                    // 
                    this.$emit('on-input-change', true);
                }
                // this.isError = false;
            }, 60),
            /**
             * @desc 
             */
            handleAddHost () {
                if (this.inputItemList.length < 1) {
                    return;
                }
                const IPReg = /(((\d+:)?)(?:(?:25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))\.){3}(?:25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d))))/;
                const ipList = [];
                //  IP 
                const errorIPList = [];
                this.inputItemList.forEach((IPText) => {
                    const IPMatch = IPText.match(IPReg);
                    if (IPMatch) {
                        const [IPStr] = IPMatch;
                        const errorIPRule = new RegExp(`([\\d:.]${encodeRegexp(IPStr)})|(${encodeRegexp(IPStr)}[\\d.])`);
                        if (errorIPRule.test(IPText)) {
                            //  IPeq: 1.1.1.12.2.2.2
                            errorIPList.push(IPText);
                        } else {
                            //  IP
                            ipList.push(IPStr);
                            //  IP 
                            const errorText = IPText.replace(new RegExp(`(${encodeRegexp(IPStr)})|(\\s)`, 'g'), '');
                            if (errorText) {
                                errorIPList.push(errorText);
                            }
                        }
                    } else {
                        //  IP
                        errorIPList.push(IPText);
                    }
                });
                
                this.isSubmiting = true;

                const params = {
                    ipList,
                };
                if (window.IPInputScope) {
                    params.actionScope = window.IPInputScope;
                }

                AppManageService.fetchHostOfHost(params)
                    .then((data) => {
                        //  IP
                        const hostList = [];
                        const hostIPMap = {};
                        
                        data.forEach((host) => {
                            const {
                                ip,
                                cloudAreaInfo,
                            } = host;
                            hostList.push({
                                realId: generateHostRealId(host),
                                ip,
                                cloudAreaInfo: {
                                    id: cloudAreaInfo.id,
                                },
                            });
                            //  IP   ID + IP 
                            hostIPMap[ip] = true;
                            hostIPMap[`${cloudAreaInfo.id}:${ip}`] = true;
                        });
                        // 
                        if (hostList.length > 0) {
                            this.$emit('on-change', 'ipInput', hostList);
                        }
                        //  IP  IP 
                        this.invalidIPList = ipList.reduce((result, IPItem) => {
                            if (!hostIPMap[IPItem]) {
                                result.push(IPItem);
                            }
                            return result;
                        }, []);
                        this.errorIPList = errorIPList;
                        // 
                        this.inputItemList = [];
                        
                        this.updateInputValue();
                        this.$emit('on-input-change', false);
                    })
                    .finally(() => {
                        this.isSubmiting = false;
                    });
            },
        },
    };
</script>
<style lang="postcss">
    .choose-ip-server-ip-input {
        padding: 20px 24px;

        .focus-error {
            textarea {
                &::selection {
                    color: #63656e;
                    background: #fdd;
                }
            }
        }

        .input-action {
            display: flex;
            align-items: center;
            margin-top: 20px;
        }

        .input-error {
            display: flex;
            margin-right: auto;
            color: #ea3636;

            .error-action {
                font-size: 16px;
                color: #63656e;
                cursor: pointer;

                &:hover {
                    color: #3a84ff;
                }
            }
        }

        .submit-btn {
            margin-left: auto;

            &:hover {
                .server-input-number .text {
                    color: #3a84ff;
                    background: #fff;
                }
            }
        }
    }

    .server-input-number,
    .server-input-number .text {
        transition: transform 0.5s;
    }

    .server-input-number {
        position: relative;
        z-index: 999999;
        display: inline-block;
        height: 17px;
        transition-timing-function: cubic-bezier(0.74, 0.95, 0.81, 0.92);
    }

    .server-input-number .text {
        display: inline-block;
        height: 17px;
        min-width: 17px;
        padding: 0 4px;
        font-size: 12px;
        line-height: 17px;
        color: #fff;
        text-align: center;
        background: #3a84ff;
        border-radius: 8px;
        transition-timing-function: cubic-bezier(0, -1.12, 0.94, -1.07);
    }
</style>
