
<template>
    <div
        ref="renderList"
        class="jb-render-list"
        v-test="{ type: 'data', value: 'table' }"
        v-bkloading="{ isLoading }">
        <bk-table
            v-if="isRendered"
            v-bind="$attrs"
            v-on="$listeners"
            :default-sort="defaultSort"
            :data="data"
            :pagination="realPagination"
            :max-height="tableMaxHeight"
            width="100%"
            @sort-change="handleSort"
            @page-change="handlePageChange"
            @page-limit-change="handleLimitChange">
            <template slot="prepend">
                <div v-if="selectNums > 0" class="jb-table-select-tips">
                    <!-- eslint-disable-next-line max-len -->
                    <span>{{ ''}}<span class="number strong">{{ selectNums }}</span>{{ ''}}</span>
                    <span class="action-clear" @click="handleClearAllSelect">{{ '' }}</span>
                </div>
                <slot name="prepend" />
            </template>
            <bk-table-column
                v-if="selectable"
                :render-header="renderHeader"
                key="listSelect"
                width="70">
                <template slot-scope="{ row }">
                    <bk-checkbox
                        :checked="!!rowSelectMemo[row[primaryKey]]"
                        @change="value => handleRowSelect(row, value)" />
                </template>
            </bk-table-column>
            <slot />
            <div v-if="isRequesting" slot="empty" style="height: 200px;" />
            <Empty v-else-if="isSearching" slot="empty" type="search">
                <div>
                    <div style="font-size: 14px; color: #63656e;">{{ '' }}</div>
                    <div style="margin-top: 8px; font-size: 12px; line-height: 16px; color: #979ba5;">
                        <span>{{ '' }}</span>
                        <template v-if="searchControl">
                            <span>{{ '' }}</span>
                            <bk-button
                                text
                                @click="handleClearSearch">
                                {{ '' }}
                            </bk-button>
                        </template>
                    </div>
                </div>
            </Empty>
        </bk-table>
    </div>
</template>
<script>
    import _ from 'lodash';
       import Empty from '@components/empty';
    import EventBus from '@utils/event-bus';
    import {
        getOffset,
        buildURLParams,
    } from '@utils/assist';
    import { routerCache } from '@utils/cache-helper';

    export default {
        components: {
            Empty,
        },
        inheritAttrs: false,
        props: {
            // 
            dataSource: {
                type: Function,
                required: true,
            },
            // name
            name: String,
            //  URL  query
            ignoreUrl: {
                type: Boolean,
                default: false,
            },
            // 
            selectable: {
                type: Boolean,
                default: false,
            },
            // 
            paginationSmall: {
                type: Boolean,
                default: false,
            },
            // data 
            primaryKey: {
                type: String,
                default: 'id',
            },
            // 
            searchControl: {
                type: Function,
            },
        },
        data () {
            return {
                isRendered: false, // 
                isRequesting: false, // api 
                isLoading: true, // api  (api  300ms  loading )
                isSearching: false, // 
                data: [{},{},{}],
                tableMaxHeight: '',
                rowSelectMemo: Object.create(null),
                selectNums: 0,
                params: {},
                pagination: {
                    count: 0,
                    current: 1,
                    limit: 10,
                },
            };
        },
        computed: {
            /**
             * @desc 
             * @returns {Boolean}
             */
            isPageChecked () {
                if (Object.keys(this.rowSelectMemo).length < 1) {
                    return false;
                }
                let status = true;
                // eslint-disable-next-line no-plusplus
                for (let i = 0; i < this.data.length; i++) {
                    if (!this.rowSelectMemo[this.data[i][this.primaryKey]]) {
                        status = false;
                        break;
                    }
                }
                return status;
            },
            /**
             * @desc 
             * @returns {Boolean}
             */
            isWholeChecked () {
                return this.selectNums > 0 && this.selectNums >= this.pagination.count;
            },
            /**
             * @desc 
             * @returns {Object}
             */
            realPagination () {
                return {
                    ...this.pagination,
                    small: this.paginationSmall,
                };
            },
        },
        watch: {
            rowSelectMemo () {
                this.selectNums = Object.keys(this.rowSelectMemo).length;
                this.data = [
                    ...this.data,
                ];
            },
        },
        created () {
            // urllistSelectIds
            const { listSelectIds } = this.$route.query;
            if (listSelectIds) {
                listSelectIds.split(',').forEach((id) => {
                    this.rowSelectMemo[this.primaryKey] = true;
                });
                setTimeout(() => {
                    this.triggerSelectChange();
                });
            }
            // render list
            this.defaultSort = {};
            this.requestParamsMemo = {};
            if (!this.ignoreUrl) {
                this.parseURL();
            }
        },
        mounted () {
            this.init();
            this.$on('onFetch', (params) => {
                if (params) {
                    this.params = Object.freeze(params);
                    if (!this.waitingInit) {
                        this.pagination.current = 1;
                    }
                    this.waitingInit = false;
                }
                const pageSize = this.pagination.limit;
                const start = parseInt(this.pagination.current - 1, 10) * pageSize;
                
                const requestParams = {
                    ...this.defaultSortParams,
                    ...this.params,
                    pageSize,
                    start,
                };
                if (!this.ignoreUrl) {
                    // 
                    routerCache.setItem(this.$route.name, requestParams);
                    window.history.replaceState({}, '', `?${buildURLParams(requestParams)}`);
                }
                
                // 
                this.requestParamsMemo = requestParams;
                this.isRequesting = true;
                // this.$request(this.dataSource(requestParams, {
                //     permission: 'page',
                // }), () => {
                //     this.isLoading = true;
                // }).then((data) => {
                const data = {
                    data: [{}, {}, {}, {}, {}, {}, {}, {}, {}],
                    total: 100,
                };
                this.pagination = {
                    ...this.pagination,
                    count: data.total || 0,
                };
                this.$emit('on-refresh', data);
                //  on-refresh 
                setTimeout(() => {
                    this.data = data.data;
                    this.isSearching = !this.checkSearchEmpty();
                });

                //  api 
                // existAny  0
                // total 
                if (!this.ignoreUrl
                    && Object.prototype.hasOwnProperty.call(data, 'existAny')
                    && !data.existAny) {
                    // page-guide, 
                    EventBus.$emit('page-empty');
                }
                // })
                // .finally(() => {
                this.isLoading = false;
                this.isRequesting = false;
                // });
            });
        },
        methods: {
            /**
             * @desc 
             */
            init () {
                const { top } = getOffset(this.$refs.renderList);
                const windowHeight = window.innerHeight;
                const tableHeadHeight = 42;
                const paginationHeight = 63;
                const windownOffsetBottom = 20;
                const listTotalHeight = windowHeight - top - tableHeadHeight - paginationHeight - windownOffsetBottom;
                const tableRowHeight = 42;
                const limit = Math.floor(listTotalHeight / tableRowHeight);
                const pageLimit = new Set([
                    10, 20, 50, 100, limit,
                ]);
                if (!pageLimit.has(this.pagination.limit)) {
                    pageLimit.add(this.pagination.limit);
                }
                this.pagination.limitList = [
                    ...pageLimit,
                ].sort((a, b) => a - b);
                if (!this.waitingInit) {
                    this.pagination.limit = limit;
                }
                this.tableMaxHeight = tableHeadHeight + tableRowHeight * limit + paginationHeight;
                this.isRendered = true;
            },
            /**
             * @desc url
             */
            parseURL () {
                this.URLQuery = this.$route.query;
                const pageSize = ~~this.URLQuery.pageSize;
                const start = ~~this.URLQuery.start;
                this.waitingInit = false;
                // url
                if (pageSize) {
                    this.pagination.current = parseInt(start / pageSize, 10) + 1;
                    this.pagination.limit = pageSize;
                    this.waitingInit = true;
                }
                // url
                const { orderField, order } = this.URLQuery;
                
                if (orderField) {
                    // table
                    this.defaultSort.prop = orderField;
                    this.defaultSort.order = ~~order ? 'ascending' : 'descending';
                }
                this.defaultSortParams = {};
                
                if (Object.prototype.hasOwnProperty.call(this.URLQuery, 'orderField')
                    && Object.prototype.hasOwnProperty.call(this.URLQuery, 'order')) {
                    // api
                    this.defaultSortParams = {
                        orderField,
                        order,
                    };
                }
            },
            /**
             * @desc  table 
             */
            resetSelect () {
                this.handleClearAllSelect();
            },
            /**
             * @desc 
             */
            fetchWhole () {
                if (this.isWholeChecked) {
                    return;
                }
                this.$request(this.dataSource({
                    ...this.requestParamsMemo,
                    pageSize: -1,
                    start: -1,
                }), () => {
                    this.isLoading = true;
                }).then((data) => {
                    const rowSelectMemo = {};
                    data.data.forEach((item) => {
                        rowSelectMemo[item[this.primaryKey]] = item;
                    });
                    this.rowSelectMemo = Object.freeze(rowSelectMemo);
                    this.triggerSelectChange();
                })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            /**
             * @desc 
             */
            renderHeader (h) {
                const renderCheckbox = () => {
                    if (this.isWholeChecked) {
                        return (
                        <div class="jb-whole-check" onClick={this.handleClearAllSelect} />
                        );
                    }
                    return (
                    <bk-checkbox checked={this.isPageChecked} nativeOnClick={this.handlePageSelectToggle} />
                    );
                };
                return (
                <div class="select-cell">
                    {renderCheckbox()}
                    <bk-popover
                        placement="bottom-start"
                        theme="light jb-table-select-menu"
                        arrow={ false }
                        size="regular">
                        <icon class="select-menu-flag" type="down-small" />
                        <div slot="content" class="jb-table-select-plan">
                            <div class="item" onClick={this.handlePageSelect}>{''}</div>
                            <div class="item" onClick={this.fetchWhole}>{''}</div>
                        </div>
                    </bk-popover>
                </div>
                );
            },
            /**
             * @desc 
             */
            triggerSelectChange () {
                const result = [];
                Object.keys(this.rowSelectMemo).forEach((idKey) => {
                    if (_.isObject(this.rowSelectMemo[idKey])) {
                        result.push(this.rowSelectMemo[idKey]);
                    } else {
                        //  urllistSelectIds
                        // { id: 1 }
                        result.push({
                            [this.primaryKey]: idKey,
                        });
                    }
                });
                this.$emit('on-selection-change', result);
            },
            /**
             * @desc 
             * @param {Object} payload 
             */
            handleSort (payload) {
                const sort = {
                    descending: 0,
                    ascending: 1,
                };
                if (payload.prop) {
                    this.params = Object.freeze({
                        ...this.params,
                        orderField: payload.prop,
                        order: sort[payload.order],
                    });
                } else {
                    const params = {
                        ...this.params,
                    };
                    delete params.orderField;
                    delete params.order;
                    this.params = Object.freeze(params);
                }
                
                this.$emit('onFetch');
            },
            /**
             * @desc 
             * @param {Number} value 
             */
            handlePageChange (value) {
                this.pagination.current = value;
                this.$emit('onFetch');
            },
            /**
             * @desc 
             * @param {Number} value 
             */
            handleLimitChange (value) {
                this.pagination.current = 1;
                this.pagination.limit = value;
                this.$emit('onFetch');
            },
            /**
             * @desc 
             */
            handlePageSelectToggle () {
                const rowSelectMemo = { ...this.rowSelectMemo };
                this.data.forEach((item) => {
                    if (this.isPageChecked) {
                        delete rowSelectMemo[item[this.primaryKey]];
                    } else {
                        rowSelectMemo[item[this.primaryKey]] = item;
                    }
                });
                this.rowSelectMemo = Object.freeze(rowSelectMemo);
                this.triggerSelectChange();
            },
            /**
             * @desc 
             */
            handlePageSelect () {
                if (this.isPageChecked) {
                    return;
                }
                const rowSelectMemo = { ...this.rowSelectMemo };
                this.data.forEach((item) => {
                    rowSelectMemo[item[this.primaryKey]] = item;
                });
                this.rowSelectMemo = Object.freeze(rowSelectMemo);

                this.triggerSelectChange();
            },
            /**
             * @desc 
             * @param {Object} row 
             * @param {Boolean} value 
             */
            handleRowSelect (row, value) {
                const rowSelectMemo = { ...this.rowSelectMemo };
                if (value) {
                    rowSelectMemo[row[this.primaryKey]] = row;
                } else {
                    delete rowSelectMemo[row[this.primaryKey]];
                }
                this.rowSelectMemo = Object.freeze(rowSelectMemo);
                this.triggerSelectChange();
            },
            /**
             * @desc 
             */
            handleClearAllSelect () {
                this.rowSelectMemo = Object.create(null);
                this.triggerSelectChange();
            },
            /**
             * @desc 
             * @returns {Boolean}
             */
            checkSearchEmpty () {
                if (!this.searchControl || typeof this.searchControl !== 'function') {
                    return false;
                }
                const searchControl = this.searchControl();
                if (!searchControl || typeof searchControl.checkEmpty !== 'function') {
                    return false;
                }
                return searchControl.checkEmpty();
            },
            /**
             * @desc 
             */
            handleClearSearch () {
                const searchControl = this.searchControl();
                if (typeof searchControl.reset === 'function') {
                    searchControl.reset();
                }
            },
        },
    };
</script>
<style lang="postcss">
    .jb-render-list {
        .bk-table-pagination-wrapper {
            background: #fff;
        }

        .bk-table-body-wrapper {
            td {
                .bk-table-setting-content {
                    display: none;
                }
            }
        }

        .select-cell {
            position: relative;
            display: flex;
            align-items: center;

            .jb-whole-check {
                position: relative;
                display: inline-block;
                width: 16px;
                height: 16px;
                vertical-align: middle;
                cursor: pointer;
                background-color: #fff;
                border: 1px solid #3a84ff;
                border-radius: 2px;

                &::after {
                    position: absolute;
                    top: 1px;
                    left: 4px;
                    width: 4px;
                    height: 8px;
                    border: 2px solid #3a84ff;
                    border-top: 0;
                    border-left: 0;
                    content: "";
                    transform: rotate(45deg);
                }
            }

            .select-menu-flag {
                margin-left: 4px;
                font-size: 18px;
            }
        }

        .jb-table-select-tips {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 30px;
            background: #ebecf0;

            .strong {
                color: inherit;
            }

            .action-clear {
                color: #3a84ff;
                cursor: pointer;
            }
        }

        .bk-table-empty-block {
            height: auto;
            background: #fff;
        }
    }

    .tippy-tooltip {
        &.jb-table-select-menu-theme {
            padding: 0;

            .jb-table-select-plan {
                padding: 5px 0;

                .item {
                    padding: 0 10px;
                    font-size: 12px;
                    line-height: 26px;
                    cursor: pointer;

                    &:hover {
                        color: #3a84ff;
                        background-color: #eaf3ff;
                    }

                    &.is-selected {
                        color: #3a84ff;
                        background-color: #f4f6fa;
                    }
                }
            }
        }
    }
</style>
